'use client'

import { useState, useEffect } from 'react'

interface InvoiceItem {
  id: string
  description: string
  quantity: number
  unitPrice: number
  total: number
}

interface InvoiceFormData {
  contactId: string
  contactName?: string
  contactEmail?: string
  dueDate: string
  items: InvoiceItem[]
  notes?: string
  currency: string
  taxRate?: number
  discountType?: 'percentage' | 'fixed'
  discountValue?: number
}

interface InvoiceFormProps {
  subAccountId: string
  initialData?: Partial<InvoiceFormData>
  contacts?: Array<{ id: string; firstName: string; lastName: string; email: string }>
  onSubmit: (data: InvoiceFormData) => Promise<void>
  onCancel: () => void
  isLoading?: boolean
}

export function InvoiceForm({ 
  subAccountId, 
  initialData, 
  contacts = [], 
  onSubmit, 
  onCancel, 
  isLoading 
}: InvoiceFormProps) {
  const [formData, setFormData] = useState<InvoiceFormData>({
    contactId: initialData?.contactId || '',
    contactName: initialData?.contactName || '',
    contactEmail: initialData?.contactEmail || '',
    dueDate: initialData?.dueDate || new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
    items: initialData?.items || [{ id: '1', description: '', quantity: 1, unitPrice: 0, total: 0 }],
    notes: initialData?.notes || '',
    currency: initialData?.currency || 'USD',
    taxRate: initialData?.taxRate || 0,
    discountType: initialData?.discountType,
    discountValue: initialData?.discountValue || 0,
  })

  const [searchContact, setSearchContact] = useState('')
  const [showContactDropdown, setShowContactDropdown] = useState(false)

  // Filter contacts based on search
  const filteredContacts = contacts.filter(c => 
    `${c.firstName} ${c.lastName}`.toLowerCase().includes(searchContact.toLowerCase()) ||
    c.email.toLowerCase().includes(searchContact.toLowerCase())
  )

  // Calculate totals
  const subtotal = formData.items.reduce((sum, item) => sum + item.total, 0)
  const discountAmount = formData.discountType === 'percentage' 
    ? subtotal * ((formData.discountValue || 0) / 100)
    : (formData.discountValue || 0)
  const taxableAmount = subtotal - discountAmount
  const taxAmount = taxableAmount * ((formData.taxRate || 0) / 100)
  const total = taxableAmount + taxAmount

  // Update item
  const updateItem = (index: number, field: keyof InvoiceItem, value: any) => {
    const newItems = [...formData.items]
    newItems[index] = { ...newItems[index], [field]: value }
    
    // Recalculate total
    if (field === 'quantity' || field === 'unitPrice') {
      newItems[index].total = newItems[index].quantity * newItems[index].unitPrice
    }
    
    setFormData({ ...formData, items: newItems })
  }

  // Add new item
  const addItem = () => {
    setFormData({
      ...formData,
      items: [...formData.items, { id: Date.now().toString(), description: '', quantity: 1, unitPrice: 0, total: 0 }]
    })
  }

  // Remove item
  const removeItem = (index: number) => {
    if (formData.items.length > 1) {
      const newItems = formData.items.filter((_, i) => i !== index)
      setFormData({ ...formData, items: newItems })
    }
  }

  // Select contact
  const selectContact = (contact: typeof contacts[0]) => {
    setFormData({
      ...formData,
      contactId: contact.id,
      contactName: `${contact.firstName} ${contact.lastName}`,
      contactEmail: contact.email,
    })
    setSearchContact(`${contact.firstName} ${contact.lastName}`)
    setShowContactDropdown(false)
  }

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.contactId && !formData.contactEmail) {
      alert('Please select a contact or enter an email address')
      return
    }
    
    if (formData.items.every(item => !item.description || item.total === 0)) {
      alert('Please add at least one item with a description and amount')
      return
    }
    
    await onSubmit(formData)
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Client Selection */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-user mr-2 text-gray-400"></i>
          Client Information
        </h3>
        
        <div className="grid grid-cols-2 gap-4">
          <div className="relative">
            <label className="block text-sm text-gray-600 mb-1">Select Contact</label>
            <input
              type="text"
              value={searchContact}
              onChange={(e) => {
                setSearchContact(e.target.value)
                setShowContactDropdown(true)
              }}
              onFocus={() => setShowContactDropdown(true)}
              placeholder="Search contacts..."
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
            {showContactDropdown && filteredContacts.length > 0 && (
              <div className="absolute z-10 w-full mt-1 bg-white border border-gray-200 rounded-lg shadow-lg max-h-48 overflow-y-auto">
                {filteredContacts.map(contact => (
                  <button
                    key={contact.id}
                    type="button"
                    onClick={() => selectContact(contact)}
                    className="w-full px-4 py-2 text-left hover:bg-gray-50 flex items-center justify-between"
                  >
                    <span className="font-medium">{contact.firstName} {contact.lastName}</span>
                    <span className="text-sm text-gray-500">{contact.email}</span>
                  </button>
                ))}
              </div>
            )}
          </div>
          
          <div>
            <label className="block text-sm text-gray-600 mb-1">Email</label>
            <input
              type="email"
              value={formData.contactEmail}
              onChange={(e) => setFormData({ ...formData, contactEmail: e.target.value })}
              placeholder="client@example.com"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
          </div>
        </div>
      </div>

      {/* Invoice Details */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-file-invoice mr-2 text-gray-400"></i>
          Invoice Details
        </h3>
        
        <div className="grid grid-cols-3 gap-4">
          <div>
            <label className="block text-sm text-gray-600 mb-1">Due Date</label>
            <input
              type="date"
              value={formData.dueDate}
              onChange={(e) => setFormData({ ...formData, dueDate: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
          </div>
          
          <div>
            <label className="block text-sm text-gray-600 mb-1">Currency</label>
            <select
              value={formData.currency}
              onChange={(e) => setFormData({ ...formData, currency: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            >
              <option value="USD">USD - US Dollar</option>
              <option value="EUR">EUR - Euro</option>
              <option value="GBP">GBP - British Pound</option>
              <option value="CAD">CAD - Canadian Dollar</option>
              <option value="AUD">AUD - Australian Dollar</option>
            </select>
          </div>
          
          <div>
            <label className="block text-sm text-gray-600 mb-1">Tax Rate (%)</label>
            <input
              type="number"
              value={formData.taxRate || ''}
              onChange={(e) => setFormData({ ...formData, taxRate: parseFloat(e.target.value) || 0 })}
              placeholder="0"
              min="0"
              max="100"
              step="0.01"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
          </div>
        </div>
      </div>

      {/* Line Items */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-list mr-2 text-gray-400"></i>
          Line Items
        </h3>
        
        <div className="space-y-3">
          <div className="grid grid-cols-12 gap-2 text-xs font-medium text-gray-500 uppercase">
            <div className="col-span-5">Description</div>
            <div className="col-span-2 text-right">Quantity</div>
            <div className="col-span-2 text-right">Unit Price</div>
            <div className="col-span-2 text-right">Total</div>
            <div className="col-span-1"></div>
          </div>
          
          {formData.items.map((item, index) => (
            <div key={item.id} className="grid grid-cols-12 gap-2 items-center">
              <div className="col-span-5">
                <input
                  type="text"
                  value={item.description}
                  onChange={(e) => updateItem(index, 'description', e.target.value)}
                  placeholder="Item description"
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>
              <div className="col-span-2">
                <input
                  type="number"
                  value={item.quantity}
                  onChange={(e) => updateItem(index, 'quantity', parseInt(e.target.value) || 0)}
                  min="1"
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm text-right focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>
              <div className="col-span-2">
                <input
                  type="number"
                  value={item.unitPrice || ''}
                  onChange={(e) => updateItem(index, 'unitPrice', parseFloat(e.target.value) || 0)}
                  placeholder="0.00"
                  min="0"
                  step="0.01"
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm text-right focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>
              <div className="col-span-2 text-right font-medium">
                ${item.total.toFixed(2)}
              </div>
              <div className="col-span-1 text-center">
                <button
                  type="button"
                  onClick={() => removeItem(index)}
                  disabled={formData.items.length === 1}
                  className="p-1 text-gray-400 hover:text-red-600 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <i className="fas fa-trash-alt"></i>
                </button>
              </div>
            </div>
          ))}
          
          <button
            type="button"
            onClick={addItem}
            className="flex items-center gap-2 px-3 py-2 text-sm text-green-600 hover:bg-green-50 rounded-lg"
          >
            <i className="fas fa-plus"></i>
            Add Line Item
          </button>
        </div>
        
        {/* Totals */}
        <div className="mt-4 pt-4 border-t border-gray-200">
          <div className="flex justify-end">
            <div className="w-64 space-y-2">
              <div className="flex justify-between text-sm">
                <span className="text-gray-500">Subtotal</span>
                <span className="font-medium">${subtotal.toFixed(2)}</span>
              </div>
              
              {discountAmount > 0 && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500">Discount</span>
                  <span className="text-red-600">-${discountAmount.toFixed(2)}</span>
                </div>
              )}
              
              {taxAmount > 0 && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500">Tax ({formData.taxRate}%)</span>
                  <span className="font-medium">${taxAmount.toFixed(2)}</span>
                </div>
              )}
              
              <div className="flex justify-between text-lg font-bold pt-2 border-t border-gray-200">
                <span>Total</span>
                <span className="text-green-600">${total.toFixed(2)}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Notes */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-sticky-note mr-2 text-gray-400"></i>
          Notes (Optional)
        </h3>
        <textarea
          value={formData.notes}
          onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
          placeholder="Add any notes or payment instructions..."
          rows={3}
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent resize-none"
        />
      </div>

      {/* Actions */}
      <div className="flex items-center justify-end gap-3">
        <button
          type="button"
          onClick={onCancel}
          disabled={isLoading}
          className="px-6 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
        >
          Cancel
        </button>
        <button
          type="submit"
          disabled={isLoading}
          className="flex items-center gap-2 px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 transition-colors"
        >
          {isLoading ? (
            <>
              <i className="fas fa-spinner fa-spin"></i>
              <span>Saving...</span>
            </>
          ) : (
            <>
              <i className="fas fa-check"></i>
              <span>Create Invoice</span>
            </>
          )}
        </button>
      </div>
    </form>
  )
}

export default InvoiceForm
