// Revenue Engine API Client
// Centralized API calls for all Revenue Engine operations

// Base API configuration
const API_BASE = '/api/v1/revenue-engine'

interface ApiResponse<T> {
  success: boolean
  data?: T
  error?: string
}

interface PaginationParams {
  page?: number
  limit?: number
  sortBy?: string
  sortOrder?: 'asc' | 'desc'
}

// Generic fetch helper
async function apiFetch<T>(
  endpoint: string,
  options?: RequestInit
): Promise<ApiResponse<T>> {
  try {
    const response = await fetch(`${API_BASE}${endpoint}`, {
      headers: {
        'Content-Type': 'application/json',
        ...options?.headers,
      },
      ...options,
    })
    
    const data = await response.json()
    
    if (!response.ok) {
      return { success: false, error: data.error || 'Request failed' }
    }
    
    return { success: true, data: data.data }
  } catch (error: any) {
    console.error('API Error:', error)
    return { success: false, error: error.message || 'Network error' }
  }
}

// ==========================================
// PRODUCTS API
// ==========================================

export const productsApi = {
  list: (subAccountId: string, params?: PaginationParams & { search?: string; status?: string; type?: string }) => {
    const searchParams = new URLSearchParams({ subAccountId, ...params as any })
    return apiFetch<{ products: any[]; pagination: any }>(`/products?${searchParams}`)
  },
  
  get: (id: string) => apiFetch<{ product: any }>(`/products/${id}`),
  
  create: (data: any) => apiFetch<{ product: any }>('/products', {
    method: 'POST',
    body: JSON.stringify(data),
  }),
  
  update: (id: string, data: any) => apiFetch<{ product: any }>(`/products/${id}`, {
    method: 'PUT',
    body: JSON.stringify(data),
  }),
  
  delete: (id: string) => apiFetch(`/products/${id}`, { method: 'DELETE' }),
}

