// ==========================================
// DOCUMENTS API (E-Signature)
// ==========================================

export const documentsApi = {
  list: (subAccountId: string, params?: PaginationParams & { 
    search?: string
    status?: string
    documentType?: string
    requiresSignature?: boolean
    contactId?: string
  }) => {
    const searchParams = new URLSearchParams({ subAccountId, ...params as any })
    return apiFetch<{ documents: any[]; pagination: any; stats: any }>(`/documents?${searchParams}`)
  },
  
  get: (id: string, accessToken?: string) => {
    const params = accessToken ? `?token=${accessToken}` : ''
    return apiFetch<{ document: any }>(`/documents/${id}${params}`)
  },
  
  create: (data: any) => apiFetch<{ document: any }>('/documents', {
    method: 'POST',
    body: JSON.stringify(data),
  }),
  
  update: (id: string, data: any) => apiFetch<{ document: any }>(`/documents/${id}`, {
    method: 'PUT',
    body: JSON.stringify(data),
  }),
  
  delete: (id: string) => apiFetch(`/documents/${id}`, { method: 'DELETE' }),
  
  send: (id: string, data?: { emailSubject?: string; emailMessage?: string; sendReminders?: boolean }) =>
    apiFetch<{ document: any; signingUrls: any[]; message: string }>(`/documents/${id}/send`, {
      method: 'POST',
      body: JSON.stringify(data || {}),
    }),
  
  sign: (id: string, data: {
    accessToken: string
    signatureData: string
    signatureType?: 'draw' | 'type' | 'upload'
    fieldValues?: Array<{ fieldId: string; value: string }>
  }) => apiFetch<{ message: string; documentStatus: string; allSigned: boolean }>(`/documents/${id}/sign`, {
    method: 'POST',
    body: JSON.stringify(data),
  }),
  
  remind: (id: string, data?: { signerIds?: string[]; emailSubject?: string; emailMessage?: string }) =>
    apiFetch<{ message: string; remindedSigners: any[] }>(`/documents/${id}/remind`, {
      method: 'POST',
      body: JSON.stringify(data || {}),
    }),
  
  void: (id: string, data?: { reason?: string; notifySigners?: boolean }) =>
    apiFetch<{ document: any; message: string }>(`/documents/${id}/void`, {
      method: 'POST',
      body: JSON.stringify(data || {}),
    }),
}

