// Revenue Engine React Hooks
// Custom hooks for data fetching and state management

import { useState, useEffect, useCallback } from 'react'
import { 
  productsApi, 
  invoicesApi, 
  ordersApi, 
  subscriptionsApi, 
  paymentLinksApi,
  paymentsApi,
  couponsApi,
  documentsApi,
  settingsApi,
  contactsApi
} from './api'

// Generic data fetching hook
function useApiData<T>(
  fetchFn: () => Promise<{ success: boolean; data?: T; error?: string }>,
  deps: any[] = []
) {
  const [data, setData] = useState<T | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const refetch = useCallback(async () => {
    setLoading(true)
    setError(null)
    const result = await fetchFn()
    if (result.success && result.data) {
      setData(result.data)
    } else {
      setError(result.error || 'Failed to fetch data')
    }
    setLoading(false)
  }, deps)

  useEffect(() => {
    refetch()
  }, [refetch])

  return { data, loading, error, refetch, setData }
}

// ==========================================
// PRODUCTS HOOKS
// ==========================================

export function useProducts(subAccountId: string, params?: { search?: string; status?: string; type?: string }) {
  return useApiData(
    () => productsApi.list(subAccountId, params),
    [subAccountId, params?.search, params?.status, params?.type]
  )
}

export function useProduct(id: string | null) {
  return useApiData(
    () => id ? productsApi.get(id) : Promise.resolve({ success: true, data: null }),
    [id]
  )
}

export function useProductMutations() {
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const createProduct = async (data: any) => {
    setLoading(true)
    setError(null)
    const result = await productsApi.create(data)
    setLoading(false)
    if (!result.success) setError(result.error || 'Failed to create product')
    return result
  }

  const updateProduct = async (id: string, data: any) => {
    setLoading(true)
    setError(null)
    const result = await productsApi.update(id, data)
    setLoading(false)
    if (!result.success) setError(result.error || 'Failed to update product')
    return result
  }

  const deleteProduct = async (id: string) => {
    setLoading(true)
    setError(null)
    const result = await productsApi.delete(id)
    setLoading(false)
    if (!result.success) setError(result.error || 'Failed to delete product')
    return result
  }

  return { createProduct, updateProduct, deleteProduct, loading, error }
}

