'use client'

import { useState } from 'react'

interface DocumentSigner {
  id: string
  name: string
  email: string
  role: 'signer' | 'viewer' | 'approver'
  signingOrder: number
  requirePin?: boolean
  requireIdVerification?: boolean
  idVerificationMethods?: string[]
}

interface DocumentField {
  id: string
  signerId?: string
  type: 'signature' | 'initials' | 'date' | 'text' | 'checkbox' | 'dropdown'
  label?: string
  placeholder?: string
  required: boolean
  pageNumber: number
  positionX: number
  positionY: number
  width: number
  height: number
  options?: string[]
}

interface DocumentFormData {
  title: string
  description: string
  documentType: 'contract' | 'agreement' | 'nda' | 'proposal' | 'other'
  content: string
  requiresSignature: boolean
  allowDecline: boolean
  expiresAt?: string
  signers: DocumentSigner[]
  fields: DocumentField[]
  attachments: string[]
}

interface DocumentFormProps {
  subAccountId: string
  initialData?: Partial<DocumentFormData>
  contacts?: Array<{ id: string; firstName: string; lastName: string; email: string }>
  onSubmit: (data: DocumentFormData) => Promise<void>
  onCancel: () => void
  isLoading?: boolean
}

export function DocumentForm({ 
  subAccountId, 
  initialData, 
  contacts = [], 
  onSubmit, 
  onCancel, 
  isLoading 
}: DocumentFormProps) {
  const [formData, setFormData] = useState<DocumentFormData>({
    title: initialData?.title || '',
    description: initialData?.description || '',
    documentType: initialData?.documentType || 'contract',
    content: initialData?.content || '',
    requiresSignature: initialData?.requiresSignature !== false,
    allowDecline: initialData?.allowDecline !== false,
    expiresAt: initialData?.expiresAt || '',
    signers: initialData?.signers || [],
    fields: initialData?.fields || [],
    attachments: initialData?.attachments || [],
  })

  const [searchContact, setSearchContact] = useState('')
  const [showContactDropdown, setShowContactDropdown] = useState(false)
  const [activeTab, setActiveTab] = useState<'details' | 'signers' | 'content'>('details')

  // Filter contacts based on search
  const filteredContacts = contacts.filter(c => 
    `${c.firstName} ${c.lastName}`.toLowerCase().includes(searchContact.toLowerCase()) ||
    c.email.toLowerCase().includes(searchContact.toLowerCase())
  )

  // Add signer
  const addSigner = (contact?: typeof contacts[0]) => {
    const newSigner: DocumentSigner = {
      id: Date.now().toString(),
      name: contact ? `${contact.firstName} ${contact.lastName}` : '',
      email: contact?.email || '',
      role: 'signer',
      signingOrder: formData.signers.length + 1,
      requirePin: false,
      requireIdVerification: false,
      idVerificationMethods: [],
    }
    setFormData({ ...formData, signers: [...formData.signers, newSigner] })
    setSearchContact('')
    setShowContactDropdown(false)
  }

  // Update signer
  const updateSigner = (index: number, field: keyof DocumentSigner, value: any) => {
    const newSigners = [...formData.signers]
    newSigners[index] = { ...newSigners[index], [field]: value }
    setFormData({ ...formData, signers: newSigners })
  }

  // Remove signer
  const removeSigner = (index: number) => {
    const newSigners = formData.signers.filter((_, i) => i !== index)
    // Reorder remaining signers
    newSigners.forEach((s, i) => s.signingOrder = i + 1)
    setFormData({ ...formData, signers: newSigners })
  }

  // Move signer up/down
  const moveSigner = (index: number, direction: 'up' | 'down') => {
    const newIndex = direction === 'up' ? index - 1 : index + 1
    if (newIndex < 0 || newIndex >= formData.signers.length) return
    
    const newSigners = [...formData.signers]
    const temp = newSigners[index]
    newSigners[index] = newSigners[newIndex]
    newSigners[newIndex] = temp
    
    // Update signing order
    newSigners.forEach((s, i) => s.signingOrder = i + 1)
    setFormData({ ...formData, signers: newSigners })
  }

  // Add field
  const addField = (type: DocumentField['type']) => {
    const newField: DocumentField = {
      id: Date.now().toString(),
      type,
      required: true,
      pageNumber: 1,
      positionX: 100,
      positionY: 100,
      width: type === 'signature' ? 200 : type === 'initials' ? 100 : 150,
      height: type === 'signature' ? 60 : type === 'checkbox' ? 24 : 40,
    }
    setFormData({ ...formData, fields: [...formData.fields, newField] })
  }

  // Update field
  const updateField = (index: number, field: keyof DocumentField, value: any) => {
    const newFields = [...formData.fields]
    newFields[index] = { ...newFields[index], [field]: value }
    setFormData({ ...formData, fields: newFields })
  }

  // Remove field
  const removeField = (index: number) => {
    const newFields = formData.fields.filter((_, i) => i !== index)
    setFormData({ ...formData, fields: newFields })
  }

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.title) {
      alert('Please enter a document title')
      return
    }
    
    if (formData.requiresSignature && formData.signers.length === 0) {
      alert('Please add at least one signer')
      return
    }
    
    await onSubmit(formData)
  }

  const documentTypes = [
    { value: 'contract', label: 'Contract', icon: 'fa-file-contract' },
    { value: 'agreement', label: 'Agreement', icon: 'fa-handshake' },
    { value: 'nda', label: 'NDA', icon: 'fa-user-secret' },
    { value: 'proposal', label: 'Proposal', icon: 'fa-file-invoice' },
    { value: 'other', label: 'Other', icon: 'fa-file' },
  ]

  const fieldTypes = [
    { type: 'signature', label: 'Signature', icon: 'fa-signature' },
    { type: 'initials', label: 'Initials', icon: 'fa-font' },
    { type: 'date', label: 'Date', icon: 'fa-calendar' },
    { type: 'text', label: 'Text', icon: 'fa-font' },
    { type: 'checkbox', label: 'Checkbox', icon: 'fa-check-square' },
    { type: 'dropdown', label: 'Dropdown', icon: 'fa-caret-down' },
  ]

  const idVerificationOptions = [
    { value: 'email_otp', label: 'Email OTP' },
    { value: 'sms_otp', label: 'SMS OTP' },
    { value: 'id_document', label: 'ID Document Upload' },
    { value: 'selfie', label: 'Selfie Verification' },
    { value: 'kba', label: 'Knowledge-Based Authentication' },
  ]

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Tab Navigation */}
      <div className="flex gap-2 border-b border-gray-200 pb-2">
        <button
          type="button"
          onClick={() => setActiveTab('details')}
          className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors ${
            activeTab === 'details' ? 'bg-green-100 text-green-700' : 'text-gray-600 hover:bg-gray-100'
          }`}
        >
          <i className="fas fa-info-circle mr-2"></i>
          Details
        </button>
        <button
          type="button"
          onClick={() => setActiveTab('signers')}
          className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors ${
            activeTab === 'signers' ? 'bg-green-100 text-green-700' : 'text-gray-600 hover:bg-gray-100'
          }`}
        >
          <i className="fas fa-users mr-2"></i>
          Signers ({formData.signers.length})
        </button>
        <button
          type="button"
          onClick={() => setActiveTab('content')}
          className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors ${
            activeTab === 'content' ? 'bg-green-100 text-green-700' : 'text-gray-600 hover:bg-gray-100'
          }`}
        >
          <i className="fas fa-file-alt mr-2"></i>
          Content
        </button>
      </div>

      {/* Details Tab */}
      {activeTab === 'details' && (
        <div className="space-y-6">
          {/* Basic Information */}
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <h3 className="text-sm font-medium text-gray-900 mb-3">
              <i className="fas fa-file-signature mr-2 text-gray-400"></i>
              Document Information
            </h3>
            
            <div className="space-y-4">
              <div>
                <label className="block text-sm text-gray-600 mb-1">Document Title *</label>
                <input
                  type="text"
                  value={formData.title}
                  onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                  placeholder="Enter document title"
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  required
                />
              </div>
              
              <div>
                <label className="block text-sm text-gray-600 mb-1">Description</label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  placeholder="Add a description (optional)"
                  rows={2}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent resize-none"
                />
              </div>
              
              <div>
                <label className="block text-sm text-gray-600 mb-2">Document Type</label>
                <div className="grid grid-cols-5 gap-2">
                  {documentTypes.map(dt => (
                    <button
                      key={dt.value}
                      type="button"
                      onClick={() => setFormData({ ...formData, documentType: dt.value as any })}
                      className={`flex flex-col items-center gap-2 p-3 rounded-lg border transition-colors ${
                        formData.documentType === dt.value
                          ? 'border-green-500 bg-green-50 text-green-700'
                          : 'border-gray-200 hover:border-gray-300'
                      }`}
                    >
                      <i className={`fas ${dt.icon} text-lg`}></i>
                      <span className="text-xs font-medium">{dt.label}</span>
                    </button>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Signature Settings */}
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <h3 className="text-sm font-medium text-gray-900 mb-3">
              <i className="fas fa-cog mr-2 text-gray-400"></i>
              Signature Settings
            </h3>
            
            <div className="space-y-4">
              <label className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <input
                  type="checkbox"
                  checked={formData.requiresSignature}
                  onChange={(e) => setFormData({ ...formData, requiresSignature: e.target.checked })}
                  className="w-5 h-5 text-green-600 border-gray-300 rounded focus:ring-green-500"
                />
                <div>
                  <span className="text-sm font-medium text-gray-900">Requires Signature</span>
                  <p className="text-xs text-gray-500">Recipients must sign this document electronically</p>
                </div>
              </label>
              
              <label className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <input
                  type="checkbox"
                  checked={formData.allowDecline}
                  onChange={(e) => setFormData({ ...formData, allowDecline: e.target.checked })}
                  className="w-5 h-5 text-green-600 border-gray-300 rounded focus:ring-green-500"
                />
                <div>
                  <span className="text-sm font-medium text-gray-900">Allow Decline</span>
                  <p className="text-xs text-gray-500">Recipients can decline to sign the document</p>
                </div>
              </label>
              
              <div>
                <label className="block text-sm text-gray-600 mb-1">Expiration Date (Optional)</label>
                <input
                  type="date"
                  value={formData.expiresAt?.split('T')[0] || ''}
                  onChange={(e) => setFormData({ ...formData, expiresAt: e.target.value ? new Date(e.target.value).toISOString() : '' })}
                  className="w-64 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Signers Tab */}
      {activeTab === 'signers' && (
        <div className="space-y-6">
          {/* Add Signer */}
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <h3 className="text-sm font-medium text-gray-900 mb-3">
              <i className="fas fa-user-plus mr-2 text-gray-400"></i>
              Add Signers
            </h3>
            
            <div className="relative">
              <div className="flex gap-2">
                <input
                  type="text"
                  value={searchContact}
                  onChange={(e) => {
                    setSearchContact(e.target.value)
                    setShowContactDropdown(true)
                  }}
                  onFocus={() => setShowContactDropdown(true)}
                  placeholder="Search contacts or enter email..."
                  className="flex-1 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
                <button
                  type="button"
                  onClick={() => addSigner()}
                  className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700"
                >
                  <i className="fas fa-plus mr-2"></i>
                  Add Signer
                </button>
              </div>
              
              {showContactDropdown && filteredContacts.length > 0 && (
                <div className="absolute z-10 w-full mt-1 bg-white border border-gray-200 rounded-lg shadow-lg max-h-48 overflow-y-auto">
                  {filteredContacts.map(contact => (
                    <button
                      key={contact.id}
                      type="button"
                      onClick={() => addSigner(contact)}
                      className="w-full px-4 py-2 text-left hover:bg-gray-50 flex items-center justify-between"
                    >
                      <span className="font-medium">{contact.firstName} {contact.lastName}</span>
                      <span className="text-sm text-gray-500">{contact.email}</span>
                    </button>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* Signers List */}
          {formData.signers.length > 0 && (
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <h3 className="text-sm font-medium text-gray-900 mb-3">
                <i className="fas fa-list-ol mr-2 text-gray-400"></i>
                Signing Order
              </h3>
              <p className="text-xs text-gray-500 mb-4">Drag to reorder or use arrows. Signers will be notified in this order.</p>
              
              <div className="space-y-3">
                {formData.signers.map((signer, index) => (
                  <div key={signer.id} className="p-4 bg-gray-50 rounded-lg">
                    <div className="flex items-start gap-3">
                      <div className="flex flex-col gap-1">
                        <button
                          type="button"
                          onClick={() => moveSigner(index, 'up')}
                          disabled={index === 0}
                          className="p-1 text-gray-400 hover:text-gray-600 disabled:opacity-30"
                        >
                          <i className="fas fa-chevron-up"></i>
                        </button>
                        <span className="w-6 h-6 flex items-center justify-center bg-green-100 text-green-700 rounded-full text-xs font-bold">
                          {index + 1}
                        </span>
                        <button
                          type="button"
                          onClick={() => moveSigner(index, 'down')}
                          disabled={index === formData.signers.length - 1}
                          className="p-1 text-gray-400 hover:text-gray-600 disabled:opacity-30"
                        >
                          <i className="fas fa-chevron-down"></i>
                        </button>
                      </div>
                      
                      <div className="flex-1 grid grid-cols-3 gap-3">
                        <div>
                          <label className="block text-xs text-gray-500 mb-1">Name</label>
                          <input
                            type="text"
                            value={signer.name}
                            onChange={(e) => updateSigner(index, 'name', e.target.value)}
                            placeholder="Full name"
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm"
                          />
                        </div>
                        <div>
                          <label className="block text-xs text-gray-500 mb-1">Email</label>
                          <input
                            type="email"
                            value={signer.email}
                            onChange={(e) => updateSigner(index, 'email', e.target.value)}
                            placeholder="email@example.com"
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm"
                          />
                        </div>
                        <div>
                          <label className="block text-xs text-gray-500 mb-1">Role</label>
                          <select
                            value={signer.role}
                            onChange={(e) => updateSigner(index, 'role', e.target.value)}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm"
                          >
                            <option value="signer">Signer</option>
                            <option value="approver">Approver</option>
                            <option value="viewer">Viewer</option>
                          </select>
                        </div>
                      </div>
                      
                      <button
                        type="button"
                        onClick={() => removeSigner(index)}
                        className="p-2 text-gray-400 hover:text-red-600"
                      >
                        <i className="fas fa-trash-alt"></i>
                      </button>
                    </div>
                    
                    {/* ID Verification Options */}
                    <div className="mt-3 pt-3 border-t border-gray-200">
                      <div className="flex flex-wrap gap-4">
                        <label className="flex items-center gap-2 text-sm">
                          <input
                            type="checkbox"
                            checked={signer.requirePin}
                            onChange={(e) => updateSigner(index, 'requirePin', e.target.checked)}
                            className="w-4 h-4 text-green-600 border-gray-300 rounded"
                          />
                          <span>Require PIN</span>
                        </label>
                        
                        <label className="flex items-center gap-2 text-sm">
                          <input
                            type="checkbox"
                            checked={signer.requireIdVerification}
                            onChange={(e) => updateSigner(index, 'requireIdVerification', e.target.checked)}
                            className="w-4 h-4 text-green-600 border-gray-300 rounded"
                          />
                          <span>Require ID Verification</span>
                        </label>
                      </div>
                      
                      {signer.requireIdVerification && (
                        <div className="mt-3">
                          <label className="block text-xs text-gray-500 mb-2">Verification Methods</label>
                          <div className="flex flex-wrap gap-2">
                            {idVerificationOptions.map(opt => (
                              <label key={opt.value} className="flex items-center gap-2 px-3 py-1 bg-white border border-gray-200 rounded-full text-xs cursor-pointer hover:border-green-500">
                                <input
                                  type="checkbox"
                                  checked={signer.idVerificationMethods?.includes(opt.value)}
                                  onChange={(e) => {
                                    const methods = signer.idVerificationMethods || []
                                    if (e.target.checked) {
                                      updateSigner(index, 'idVerificationMethods', [...methods, opt.value])
                                    } else {
                                      updateSigner(index, 'idVerificationMethods', methods.filter(m => m !== opt.value))
                                    }
                                  }}
                                  className="w-3 h-3 text-green-600 border-gray-300 rounded"
                                />
                                {opt.label}
                              </label>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {formData.signers.length === 0 && (
            <div className="text-center py-12 bg-gray-50 rounded-lg border border-dashed border-gray-300">
              <i className="fas fa-users text-4xl text-gray-300 mb-3"></i>
              <p className="text-gray-500">No signers added yet</p>
              <p className="text-sm text-gray-400">Add signers who need to sign this document</p>
            </div>
          )}
        </div>
      )}

      {/* Content Tab */}
      {activeTab === 'content' && (
        <div className="space-y-6">
          {/* Document Content */}
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <h3 className="text-sm font-medium text-gray-900 mb-3">
              <i className="fas fa-edit mr-2 text-gray-400"></i>
              Document Content
            </h3>
            
            <textarea
              value={formData.content}
              onChange={(e) => setFormData({ ...formData, content: e.target.value })}
              placeholder="Enter your document content here... You can use HTML for formatting."
              rows={15}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent font-mono text-sm resize-none"
            />
            
            <p className="text-xs text-gray-500 mt-2">
              <i className="fas fa-info-circle mr-1"></i>
              Tip: You can use HTML tags for formatting. Signature fields will be added in the document editor.
            </p>
          </div>

          {/* Signature Fields */}
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <h3 className="text-sm font-medium text-gray-900 mb-3">
              <i className="fas fa-signature mr-2 text-gray-400"></i>
              Signature Fields
            </h3>
            
            <div className="flex flex-wrap gap-2 mb-4">
              {fieldTypes.map(ft => (
                <button
                  key={ft.type}
                  type="button"
                  onClick={() => addField(ft.type as any)}
                  className="flex items-center gap-2 px-3 py-2 bg-gray-100 hover:bg-gray-200 rounded-lg text-sm transition-colors"
                >
                  <i className={`fas ${ft.icon} text-gray-500`}></i>
                  {ft.label}
                </button>
              ))}
            </div>
            
            {formData.fields.length > 0 && (
              <div className="space-y-2">
                {formData.fields.map((field, index) => (
                  <div key={field.id} className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                    <i className={`fas ${fieldTypes.find(ft => ft.type === field.type)?.icon} text-gray-400`}></i>
                    <span className="text-sm font-medium capitalize">{field.type}</span>
                    
                    <select
                      value={field.signerId || ''}
                      onChange={(e) => updateField(index, 'signerId', e.target.value || undefined)}
                      className="px-2 py-1 border border-gray-300 rounded text-sm"
                    >
                      <option value="">Any Signer</option>
                      {formData.signers.map(s => (
                        <option key={s.id} value={s.id}>{s.name || s.email}</option>
                      ))}
                    </select>
                    
                    <label className="flex items-center gap-1 text-sm">
                      <input
                        type="checkbox"
                        checked={field.required}
                        onChange={(e) => updateField(index, 'required', e.target.checked)}
                        className="w-4 h-4 text-green-600 border-gray-300 rounded"
                      />
                      Required
                    </label>
                    
                    {(field.type === 'text' || field.type === 'dropdown') && (
                      <input
                        type="text"
                        value={field.label || ''}
                        onChange={(e) => updateField(index, 'label', e.target.value)}
                        placeholder="Label"
                        className="px-2 py-1 border border-gray-300 rounded text-sm"
                      />
                    )}
                    
                    <button
                      type="button"
                      onClick={() => removeField(index)}
                      className="ml-auto p-1 text-gray-400 hover:text-red-600"
                    >
                      <i className="fas fa-times"></i>
                    </button>
                  </div>
                ))}
              </div>
            )}
            
            {formData.fields.length === 0 && (
              <p className="text-sm text-gray-500">
                Click the buttons above to add signature fields to your document.
              </p>
            )}
          </div>
        </div>
      )}

      {/* Actions */}
      <div className="flex items-center justify-end gap-3 pt-4 border-t border-gray-200">
        <button
          type="button"
          onClick={onCancel}
          disabled={isLoading}
          className="px-6 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
        >
          Cancel
        </button>
        <button
          type="submit"
          disabled={isLoading}
          className="flex items-center gap-2 px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 transition-colors"
        >
          {isLoading ? (
            <>
              <i className="fas fa-spinner fa-spin"></i>
              <span>Saving...</span>
            </>
          ) : (
            <>
              <i className="fas fa-check"></i>
              <span>{initialData ? 'Update Document' : 'Create Document'}</span>
            </>
          )}
        </button>
      </div>
    </form>
  )
}

export default DocumentForm
