'use client'

import { useState, useRef, useCallback } from 'react'

interface SignatureField {
  id: string
  type: 'signature' | 'initials' | 'date' | 'text' | 'checkbox'
  x: number
  y: number
  width: number
  height: number
  page: number
  signerId?: string
  label?: string
  required: boolean
  value?: string
}

interface Signer {
  id: string
  name: string
  email: string
  color: string
}

interface DocumentEditorProps {
  documentUrl?: string
  documentContent?: string
  fields: SignatureField[]
  signers: Signer[]
  onFieldsChange: (fields: SignatureField[]) => void
  onSignersChange: (signers: Signer[]) => void
  mode?: 'edit' | 'sign' | 'view'
  currentSignerId?: string
}

const FIELD_TYPES = [
  { type: 'signature', label: 'Signature', icon: 'fa-signature' },
  { type: 'initials', label: 'Initials', icon: 'fa-font' },
  { type: 'date', label: 'Date', icon: 'fa-calendar' },
  { type: 'text', label: 'Text Field', icon: 'fa-i-cursor' },
  { type: 'checkbox', label: 'Checkbox', icon: 'fa-check-square' },
]

const SIGNER_COLORS = ['#3B82F6', '#10B981', '#F59E0B', '#EF4444', '#8B5CF6', '#EC4899']

export function DocumentEditor({
  documentUrl,
  documentContent,
  fields,
  signers,
  onFieldsChange,
  onSignersChange,
  mode = 'edit',
  currentSignerId,
}: DocumentEditorProps) {
  const [selectedFieldType, setSelectedFieldType] = useState<string | null>(null)
  const [selectedSignerId, setSelectedSignerId] = useState<string | null>(signers[0]?.id || null)
  const [selectedField, setSelectedField] = useState<string | null>(null)
  const [isDragging, setIsDragging] = useState(false)
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 })
  const [currentPage, setCurrentPage] = useState(1)
  const [totalPages] = useState(1)
  const [zoom, setZoom] = useState(100)
  
  const editorRef = useRef<HTMLDivElement>(null)
  const [showAddSigner, setShowAddSigner] = useState(false)
  const [newSignerName, setNewSignerName] = useState('')
  const [newSignerEmail, setNewSignerEmail] = useState('')

  // Add a new signer
  const addSigner = () => {
    if (!newSignerName || !newSignerEmail) return
    
    const newSigner: Signer = {
      id: `signer-${Date.now()}`,
      name: newSignerName,
      email: newSignerEmail,
      color: SIGNER_COLORS[signers.length % SIGNER_COLORS.length],
    }
    
    onSignersChange([...signers, newSigner])
    setNewSignerName('')
    setNewSignerEmail('')
    setShowAddSigner(false)
    setSelectedSignerId(newSigner.id)
  }

  // Remove a signer
  const removeSigner = (signerId: string) => {
    onSignersChange(signers.filter(s => s.id !== signerId))
    // Remove fields assigned to this signer
    onFieldsChange(fields.filter(f => f.signerId !== signerId))
    if (selectedSignerId === signerId) {
      setSelectedSignerId(signers.find(s => s.id !== signerId)?.id || null)
    }
  }

  // Add a field to the document
  const addField = useCallback((e: React.MouseEvent<HTMLDivElement>) => {
    if (!selectedFieldType || !selectedSignerId || mode !== 'edit') return
    
    const rect = editorRef.current?.getBoundingClientRect()
    if (!rect) return
    
    const x = ((e.clientX - rect.left) / rect.width) * 100
    const y = ((e.clientY - rect.top) / rect.height) * 100
    
    const fieldSizes: Record<string, { width: number; height: number }> = {
      signature: { width: 20, height: 8 },
      initials: { width: 10, height: 6 },
      date: { width: 15, height: 4 },
      text: { width: 20, height: 4 },
      checkbox: { width: 3, height: 3 },
    }
    
    const size = fieldSizes[selectedFieldType] || { width: 15, height: 5 }
    
    const newField: SignatureField = {
      id: `field-${Date.now()}`,
      type: selectedFieldType as SignatureField['type'],
      x: Math.max(0, Math.min(x - size.width / 2, 100 - size.width)),
      y: Math.max(0, Math.min(y - size.height / 2, 100 - size.height)),
      width: size.width,
      height: size.height,
      page: currentPage,
      signerId: selectedSignerId,
      required: true,
    }
    
    onFieldsChange([...fields, newField])
    setSelectedField(newField.id)
    setSelectedFieldType(null)
  }, [selectedFieldType, selectedSignerId, mode, currentPage, fields, onFieldsChange])

  // Handle field drag start
  const handleFieldDragStart = (e: React.MouseEvent, field: SignatureField) => {
    if (mode !== 'edit') return
    e.stopPropagation()
    
    const rect = editorRef.current?.getBoundingClientRect()
    if (!rect) return
    
    const fieldX = (field.x / 100) * rect.width
    const fieldY = (field.y / 100) * rect.height
    
    setDragOffset({
      x: e.clientX - rect.left - fieldX,
      y: e.clientY - rect.top - fieldY,
    })
    setIsDragging(true)
    setSelectedField(field.id)
  }

  // Handle field drag
  const handleFieldDrag = useCallback((e: React.MouseEvent) => {
    if (!isDragging || !selectedField || mode !== 'edit') return
    
    const rect = editorRef.current?.getBoundingClientRect()
    if (!rect) return
    
    const x = ((e.clientX - rect.left - dragOffset.x) / rect.width) * 100
    const y = ((e.clientY - rect.top - dragOffset.y) / rect.height) * 100
    
    const field = fields.find(f => f.id === selectedField)
    if (!field) return
    
    const newX = Math.max(0, Math.min(x, 100 - field.width))
    const newY = Math.max(0, Math.min(y, 100 - field.height))
    
    onFieldsChange(fields.map(f => 
      f.id === selectedField ? { ...f, x: newX, y: newY } : f
    ))
  }, [isDragging, selectedField, mode, dragOffset, fields, onFieldsChange])

  // Handle field drag end
  const handleFieldDragEnd = () => {
    setIsDragging(false)
  }

  // Delete selected field
  const deleteField = (fieldId: string) => {
    onFieldsChange(fields.filter(f => f.id !== fieldId))
    setSelectedField(null)
  }

  // Get signer color for a field
  const getSignerColor = (signerId?: string) => {
    if (!signerId) return '#9CA3AF'
    return signers.find(s => s.id === signerId)?.color || '#9CA3AF'
  }

  // Get signer name for a field
  const getSignerName = (signerId?: string) => {
    if (!signerId) return 'Unassigned'
    return signers.find(s => s.id === signerId)?.name || 'Unknown'
  }

  return (
    <div className="flex h-[calc(100vh-200px)] min-h-[600px] bg-gray-100 rounded-lg overflow-hidden">
      {/* Left Sidebar - Tools */}
      {mode === 'edit' && (
        <div className="w-64 bg-white border-r border-gray-200 flex flex-col">
          {/* Signers Section */}
          <div className="p-4 border-b border-gray-200">
            <h3 className="text-sm font-semibold text-gray-900 mb-3">
              <i className="fas fa-users mr-2 text-gray-400"></i>
              Signers
            </h3>
            <div className="space-y-2">
              {signers.map((signer) => (
                <div
                  key={signer.id}
                  onClick={() => setSelectedSignerId(signer.id)}
                  className={`flex items-center justify-between p-2 rounded-lg cursor-pointer transition-colors ${
                    selectedSignerId === signer.id ? 'bg-gray-100' : 'hover:bg-gray-50'
                  }`}
                >
                  <div className="flex items-center gap-2">
                    <div
                      className="w-3 h-3 rounded-full"
                      style={{ backgroundColor: signer.color }}
                    />
                    <div>
                      <p className="text-sm font-medium text-gray-900">{signer.name}</p>
                      <p className="text-xs text-gray-500">{signer.email}</p>
                    </div>
                  </div>
                  <button
                    onClick={(e) => {
                      e.stopPropagation()
                      removeSigner(signer.id)
                    }}
                    className="p-1 text-gray-400 hover:text-red-600"
                  >
                    <i className="fas fa-times text-xs"></i>
                  </button>
                </div>
              ))}
            </div>
            
            {showAddSigner ? (
              <div className="mt-3 space-y-2">
                <input
                  type="text"
                  placeholder="Name"
                  value={newSignerName}
                  onChange={(e) => setNewSignerName(e.target.value)}
                  className="w-full px-3 py-2 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500"
                />
                <input
                  type="email"
                  placeholder="Email"
                  value={newSignerEmail}
                  onChange={(e) => setNewSignerEmail(e.target.value)}
                  className="w-full px-3 py-2 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500"
                />
                <div className="flex gap-2">
                  <button
                    onClick={addSigner}
                    className="flex-1 px-3 py-1 text-sm bg-green-600 text-white rounded-lg hover:bg-green-700"
                  >
                    Add
                  </button>
                  <button
                    onClick={() => setShowAddSigner(false)}
                    className="px-3 py-1 text-sm text-gray-600 hover:bg-gray-100 rounded-lg"
                  >
                    Cancel
                  </button>
                </div>
              </div>
            ) : (
              <button
                onClick={() => setShowAddSigner(true)}
                className="mt-3 w-full flex items-center justify-center gap-2 px-3 py-2 text-sm text-green-600 hover:bg-green-50 rounded-lg border border-dashed border-green-300"
              >
                <i className="fas fa-plus"></i>
                Add Signer
              </button>
            )}
          </div>

          {/* Fields Section */}
          <div className="p-4 flex-1 overflow-y-auto">
            <h3 className="text-sm font-semibold text-gray-900 mb-3">
              <i className="fas fa-edit mr-2 text-gray-400"></i>
              Add Fields
            </h3>
            <p className="text-xs text-gray-500 mb-3">
              Click a field type, then click on the document to place it
            </p>
            <div className="space-y-2">
              {FIELD_TYPES.map((fieldType) => (
                <button
                  key={fieldType.type}
                  onClick={() => setSelectedFieldType(
                    selectedFieldType === fieldType.type ? null : fieldType.type
                  )}
                  className={`w-full flex items-center gap-3 px-3 py-2 text-sm rounded-lg transition-colors ${
                    selectedFieldType === fieldType.type
                      ? 'bg-green-100 text-green-700 border border-green-300'
                      : 'text-gray-700 hover:bg-gray-100 border border-transparent'
                  }`}
                >
                  <i className={`fas ${fieldType.icon} w-5 text-center`}></i>
                  {fieldType.label}
                </button>
              ))}
            </div>
          </div>

          {/* Field Properties */}
          {selectedField && (
            <div className="p-4 border-t border-gray-200">
              <h3 className="text-sm font-semibold text-gray-900 mb-3">
                <i className="fas fa-cog mr-2 text-gray-400"></i>
                Field Properties
              </h3>
              {(() => {
                const field = fields.find(f => f.id === selectedField)
                if (!field) return null
                return (
                  <div className="space-y-3">
                    <div>
                      <label className="block text-xs text-gray-500 mb-1">Assigned To</label>
                      <select
                        value={field.signerId || ''}
                        onChange={(e) => {
                          onFieldsChange(fields.map(f => 
                            f.id === selectedField ? { ...f, signerId: e.target.value } : f
                          ))
                        }}
                        className="w-full px-3 py-2 text-sm border border-gray-300 rounded-lg"
                      >
                        {signers.map(signer => (
                          <option key={signer.id} value={signer.id}>{signer.name}</option>
                        ))}
                      </select>
                    </div>
                    <div className="flex items-center gap-2">
                      <input
                        type="checkbox"
                        id="field-required"
                        checked={field.required}
                        onChange={(e) => {
                          onFieldsChange(fields.map(f => 
                            f.id === selectedField ? { ...f, required: e.target.checked } : f
                          ))
                        }}
                        className="rounded text-green-600"
                      />
                      <label htmlFor="field-required" className="text-sm text-gray-700">Required</label>
                    </div>
                    <button
                      onClick={() => deleteField(selectedField)}
                      className="w-full px-3 py-2 text-sm text-red-600 hover:bg-red-50 rounded-lg border border-red-200"
                    >
                      <i className="fas fa-trash mr-2"></i>
                      Delete Field
                    </button>
                  </div>
                )
              })()}
            </div>
          )}
        </div>
      )}

      {/* Document Viewer */}
      <div className="flex-1 flex flex-col">
        {/* Toolbar */}
        <div className="flex items-center justify-between px-4 py-2 bg-white border-b border-gray-200">
          <div className="flex items-center gap-2">
            <button
              onClick={() => setCurrentPage(Math.max(1, currentPage - 1))}
              disabled={currentPage === 1}
              className="p-2 text-gray-600 hover:bg-gray-100 rounded-lg disabled:opacity-50"
            >
              <i className="fas fa-chevron-left"></i>
            </button>
            <span className="text-sm text-gray-600">
              Page {currentPage} of {totalPages}
            </span>
            <button
              onClick={() => setCurrentPage(Math.min(totalPages, currentPage + 1))}
              disabled={currentPage === totalPages}
              className="p-2 text-gray-600 hover:bg-gray-100 rounded-lg disabled:opacity-50"
            >
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
          
          <div className="flex items-center gap-2">
            <button
              onClick={() => setZoom(Math.max(50, zoom - 10))}
              className="p-2 text-gray-600 hover:bg-gray-100 rounded-lg"
            >
              <i className="fas fa-search-minus"></i>
            </button>
            <span className="text-sm text-gray-600 w-12 text-center">{zoom}%</span>
            <button
              onClick={() => setZoom(Math.min(200, zoom + 10))}
              className="p-2 text-gray-600 hover:bg-gray-100 rounded-lg"
            >
              <i className="fas fa-search-plus"></i>
            </button>
          </div>
        </div>

        {/* Document Canvas */}
        <div className="flex-1 overflow-auto p-8 flex justify-center">
          <div
            ref={editorRef}
            onClick={selectedFieldType ? addField : undefined}
            onMouseMove={isDragging ? handleFieldDrag : undefined}
            onMouseUp={isDragging ? handleFieldDragEnd : undefined}
            onMouseLeave={isDragging ? handleFieldDragEnd : undefined}
            className={`relative bg-white shadow-lg ${
              selectedFieldType ? 'cursor-crosshair' : ''
            }`}
            style={{
              width: `${(8.5 * zoom)}px`,
              height: `${(11 * zoom)}px`,
              minHeight: '600px',
            }}
          >
            {/* Document Content */}
            {documentUrl ? (
              <img
                src={documentUrl}
                alt="Document"
                className="w-full h-full object-contain"
                draggable={false}
              />
            ) : documentContent ? (
              <div
                className="w-full h-full p-8 prose max-w-none"
                dangerouslySetInnerHTML={{ __html: documentContent }}
              />
            ) : (
              <div className="w-full h-full flex items-center justify-center">
                <div className="text-center text-gray-400">
                  <i className="fas fa-file-alt text-6xl mb-4"></i>
                  <p>No document loaded</p>
                  <p className="text-sm">Upload a document or create content</p>
                </div>
              </div>
            )}

            {/* Signature Fields */}
            {fields
              .filter(f => f.page === currentPage)
              .map((field) => {
                const isSelected = selectedField === field.id
                const signerColor = getSignerColor(field.signerId)
                const isCurrentSigner = mode === 'sign' && field.signerId === currentSignerId
                
                return (
                  <div
                    key={field.id}
                    onClick={(e) => {
                      e.stopPropagation()
                      setSelectedField(field.id)
                    }}
                    onMouseDown={(e) => handleFieldDragStart(e, field)}
                    className={`absolute border-2 rounded transition-all ${
                      isSelected ? 'ring-2 ring-blue-400' : ''
                    } ${mode === 'edit' ? 'cursor-move' : ''} ${
                      isCurrentSigner ? 'cursor-pointer hover:opacity-80' : ''
                    }`}
                    style={{
                      left: `${field.x}%`,
                      top: `${field.y}%`,
                      width: `${field.width}%`,
                      height: `${field.height}%`,
                      borderColor: signerColor,
                      backgroundColor: `${signerColor}15`,
                    }}
                  >
                    <div className="w-full h-full flex items-center justify-center text-xs" style={{ color: signerColor }}>
                      {field.value ? (
                        field.type === 'signature' || field.type === 'initials' ? (
                          <img src={field.value} alt="Signature" className="max-w-full max-h-full" />
                        ) : (
                          <span>{field.value}</span>
                        )
                      ) : (
                        <span className="opacity-60">
                          {field.type === 'signature' && <><i className="fas fa-signature mr-1"></i>Sign</>}
                          {field.type === 'initials' && <><i className="fas fa-font mr-1"></i>Initials</>}
                          {field.type === 'date' && <><i className="fas fa-calendar mr-1"></i>Date</>}
                          {field.type === 'text' && <><i className="fas fa-i-cursor mr-1"></i>Text</>}
                          {field.type === 'checkbox' && <i className="fas fa-square"></i>}
                        </span>
                      )}
                    </div>
                    
                    {/* Signer Label */}
                    {mode === 'edit' && (
                      <div
                        className="absolute -top-5 left-0 text-xs px-1 rounded-t whitespace-nowrap"
                        style={{ backgroundColor: signerColor, color: 'white' }}
                      >
                        {getSignerName(field.signerId)}
                      </div>
                    )}
                  </div>
                )
              })}
          </div>
        </div>
      </div>

      {/* Right Sidebar - Field List (in edit mode) */}
      {mode === 'edit' && fields.length > 0 && (
        <div className="w-64 bg-white border-l border-gray-200 p-4 overflow-y-auto">
          <h3 className="text-sm font-semibold text-gray-900 mb-3">
            <i className="fas fa-list mr-2 text-gray-400"></i>
            Fields ({fields.length})
          </h3>
          <div className="space-y-2">
            {fields.map((field, index) => (
              <div
                key={field.id}
                onClick={() => setSelectedField(field.id)}
                className={`p-2 rounded-lg cursor-pointer transition-colors ${
                  selectedField === field.id ? 'bg-gray-100' : 'hover:bg-gray-50'
                }`}
              >
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-2">
                    <div
                      className="w-2 h-2 rounded-full"
                      style={{ backgroundColor: getSignerColor(field.signerId) }}
                    />
                    <span className="text-sm font-medium text-gray-900 capitalize">
                      {field.type} {index + 1}
                    </span>
                  </div>
                  {field.required && (
                    <span className="text-xs text-red-500">Required</span>
                  )}
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  {getSignerName(field.signerId)}
                </p>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  )
}

export default DocumentEditor
