'use client'

import { useState } from 'react'

interface CouponFormData {
  code: string
  discountType: 'percentage' | 'fixed_amount'
  discountValue: number
  currency?: string
  usageLimit?: number
  validFrom?: string
  validUntil?: string
  minimumAmount?: number
  applyTo?: 'all' | 'specific_products' | 'specific_collections'
  productIds?: string[]
  collectionIds?: string[]
}

interface CouponFormProps {
  subAccountId: string
  initialData?: Partial<CouponFormData>
  products?: Array<{ id: string; name: string }>
  collections?: Array<{ id: string; name: string }>
  onSubmit: (data: CouponFormData) => Promise<void>
  onCancel: () => void
  isLoading?: boolean
}

export function CouponForm({
  subAccountId,
  initialData,
  products = [],
  collections = [],
  onSubmit,
  onCancel,
  isLoading,
}: CouponFormProps) {
  const [formData, setFormData] = useState<CouponFormData>({
    code: initialData?.code || '',
    discountType: initialData?.discountType || 'percentage',
    discountValue: initialData?.discountValue || 0,
    currency: initialData?.currency || 'USD',
    usageLimit: initialData?.usageLimit,
    validFrom: initialData?.validFrom,
    validUntil: initialData?.validUntil,
    minimumAmount: initialData?.minimumAmount,
    applyTo: initialData?.applyTo || 'all',
    productIds: initialData?.productIds || [],
    collectionIds: initialData?.collectionIds || [],
  })

  // Generate random code
  const generateCode = () => {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789'
    let code = ''
    for (let i = 0; i < 8; i++) {
      code += chars.charAt(Math.floor(Math.random() * chars.length))
    }
    setFormData({ ...formData, code })
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.code || formData.discountValue <= 0) {
      alert('Please enter a coupon code and valid discount value')
      return
    }
    
    if (formData.discountType === 'percentage' && formData.discountValue > 100) {
      alert('Percentage discount cannot exceed 100%')
      return
    }
    
    await onSubmit(formData)
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Coupon Code */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-ticket-alt mr-2 text-gray-400"></i>
          Coupon Code
        </h3>
        
        <div className="flex gap-2">
          <div className="flex-1">
            <input
              type="text"
              value={formData.code}
              onChange={(e) => setFormData({ ...formData, code: e.target.value.toUpperCase() })}
              placeholder="e.g., SUMMER20"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent font-mono uppercase"
              required
            />
          </div>
          <button
            type="button"
            onClick={generateCode}
            className="px-4 py-2 text-green-600 hover:bg-green-50 rounded-lg border border-green-300"
          >
            <i className="fas fa-random mr-2"></i>
            Generate
          </button>
        </div>
      </div>

      {/* Discount Details */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-percent mr-2 text-gray-400"></i>
          Discount Details
        </h3>
        
        <div className="space-y-4">
          <div>
            <label className="block text-sm text-gray-600 mb-2">Discount Type</label>
            <div className="flex gap-4">
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="radio"
                  checked={formData.discountType === 'percentage'}
                  onChange={() => setFormData({ ...formData, discountType: 'percentage' })}
                  className="text-green-600"
                />
                <span className="text-sm text-gray-700">Percentage</span>
              </label>
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="radio"
                  checked={formData.discountType === 'fixed_amount'}
                  onChange={() => setFormData({ ...formData, discountType: 'fixed_amount' })}
                  className="text-green-600"
                />
                <span className="text-sm text-gray-700">Fixed Amount</span>
              </label>
            </div>
          </div>
          
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm text-gray-600 mb-1">Discount Value *</label>
              <div className="relative">
                <span className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500">
                  {formData.discountType === 'percentage' ? '%' : '$'}
                </span>
                <input
                  type="number"
                  value={formData.discountValue || ''}
                  onChange={(e) => setFormData({ ...formData, discountValue: parseFloat(e.target.value) || 0 })}
                  placeholder="0"
                  min="0"
                  max={formData.discountType === 'percentage' ? 100 : undefined}
                  step={formData.discountType === 'percentage' ? 1 : 0.01}
                  className="w-full pl-8 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  required
                />
              </div>
            </div>
            
            {formData.discountType === 'fixed_amount' && (
              <div>
                <label className="block text-sm text-gray-600 mb-1">Currency</label>
                <select
                  value={formData.currency}
                  onChange={(e) => setFormData({ ...formData, currency: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                >
                  <option value="USD">USD - US Dollar</option>
                  <option value="EUR">EUR - Euro</option>
                  <option value="GBP">GBP - British Pound</option>
                </select>
              </div>
            )}
          </div>
          
          <div>
            <label className="block text-sm text-gray-600 mb-1">Minimum Purchase Amount</label>
            <div className="relative w-1/2">
              <span className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="number"
                value={formData.minimumAmount || ''}
                onChange={(e) => setFormData({ ...formData, minimumAmount: parseFloat(e.target.value) || undefined })}
                placeholder="No minimum"
                min="0"
                step="0.01"
                className="w-full pl-8 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
              />
            </div>
          </div>
        </div>
      </div>

      {/* Usage Limits */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-chart-bar mr-2 text-gray-400"></i>
          Usage Limits
        </h3>
        
        <div className="grid grid-cols-3 gap-4">
          <div>
            <label className="block text-sm text-gray-600 mb-1">Total Usage Limit</label>
            <input
              type="number"
              value={formData.usageLimit || ''}
              onChange={(e) => setFormData({ ...formData, usageLimit: parseInt(e.target.value) || undefined })}
              placeholder="Unlimited"
              min="1"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
          </div>
          
          <div>
            <label className="block text-sm text-gray-600 mb-1">Valid From</label>
            <input
              type="date"
              value={formData.validFrom ? formData.validFrom.split('T')[0] : ''}
              onChange={(e) => setFormData({ ...formData, validFrom: e.target.value ? new Date(e.target.value).toISOString() : undefined })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
          </div>
          
          <div>
            <label className="block text-sm text-gray-600 mb-1">Valid Until</label>
            <input
              type="date"
              value={formData.validUntil ? formData.validUntil.split('T')[0] : ''}
              onChange={(e) => setFormData({ ...formData, validUntil: e.target.value ? new Date(e.target.value).toISOString() : undefined })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
          </div>
        </div>
      </div>

      {/* Apply To */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-3">
          <i className="fas fa-bullseye mr-2 text-gray-400"></i>
          Apply To
        </h3>
        
        <div className="space-y-3">
          <label className="flex items-center gap-2 cursor-pointer">
            <input
              type="radio"
              checked={formData.applyTo === 'all'}
              onChange={() => setFormData({ ...formData, applyTo: 'all', productIds: [], collectionIds: [] })}
              className="text-green-600"
            />
            <span className="text-sm text-gray-700">All products</span>
          </label>
          <label className="flex items-center gap-2 cursor-pointer">
            <input
              type="radio"
              checked={formData.applyTo === 'specific_products'}
              onChange={() => setFormData({ ...formData, applyTo: 'specific_products', collectionIds: [] })}
              className="text-green-600"
            />
            <span className="text-sm text-gray-700">Specific products</span>
          </label>
          <label className="flex items-center gap-2 cursor-pointer">
            <input
              type="radio"
              checked={formData.applyTo === 'specific_collections'}
              onChange={() => setFormData({ ...formData, applyTo: 'specific_collections', productIds: [] })}
              className="text-green-600"
            />
            <span className="text-sm text-gray-700">Specific collections</span>
          </label>
        </div>
        
        {formData.applyTo === 'specific_products' && products.length > 0 && (
          <div className="mt-4 border-t border-gray-200 pt-4">
            <label className="block text-sm text-gray-600 mb-2">Select Products</label>
            <div className="max-h-40 overflow-y-auto border border-gray-200 rounded-lg p-2 space-y-1">
              {products.map((product) => (
                <label key={product.id} className="flex items-center gap-2 p-2 hover:bg-gray-50 rounded cursor-pointer">
                  <input
                    type="checkbox"
                    checked={formData.productIds?.includes(product.id)}
                    onChange={(e) => {
                      if (e.target.checked) {
                        setFormData({ ...formData, productIds: [...(formData.productIds || []), product.id] })
                      } else {
                        setFormData({ ...formData, productIds: formData.productIds?.filter(id => id !== product.id) })
                      }
                    }}
                    className="rounded text-green-600"
                  />
                  <span className="text-sm text-gray-700">{product.name}</span>
                </label>
              ))}
            </div>
          </div>
        )}
        
        {formData.applyTo === 'specific_collections' && collections.length > 0 && (
          <div className="mt-4 border-t border-gray-200 pt-4">
            <label className="block text-sm text-gray-600 mb-2">Select Collections</label>
            <div className="max-h-40 overflow-y-auto border border-gray-200 rounded-lg p-2 space-y-1">
              {collections.map((collection) => (
                <label key={collection.id} className="flex items-center gap-2 p-2 hover:bg-gray-50 rounded cursor-pointer">
                  <input
                    type="checkbox"
                    checked={formData.collectionIds?.includes(collection.id)}
                    onChange={(e) => {
                      if (e.target.checked) {
                        setFormData({ ...formData, collectionIds: [...(formData.collectionIds || []), collection.id] })
                      } else {
                        setFormData({ ...formData, collectionIds: formData.collectionIds?.filter(id => id !== collection.id) })
                      }
                    }}
                    className="rounded text-green-600"
                  />
                  <span className="text-sm text-gray-700">{collection.name}</span>
                </label>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Actions */}
      <div className="flex items-center justify-end gap-3">
        <button
          type="button"
          onClick={onCancel}
          disabled={isLoading}
          className="px-6 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
        >
          Cancel
        </button>
        <button
          type="submit"
          disabled={isLoading}
          className="flex items-center gap-2 px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 transition-colors"
        >
          {isLoading ? (
            <>
              <i className="fas fa-spinner fa-spin"></i>
              <span>Creating...</span>
            </>
          ) : (
            <>
              <i className="fas fa-check"></i>
              <span>Create Coupon</span>
            </>
          )}
        </button>
      </div>
    </form>
  )
}

export default CouponForm
