function InvoicesTab() {
  const [activeSubTab, setActiveSubTab] = useState('all')
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [statusFilter, setStatusFilter] = useState('')
  
  const { data, loading, error, refetch } = useInvoices(SUB_ACCOUNT_ID, { 
    search: searchQuery,
    status: statusFilter || undefined 
  })
  const { createInvoice, sendInvoice, loading: mutationLoading } = useInvoiceMutations()

  const invoices = data?.invoices || []
  const stats = data?.stats || { totalOutstanding: 0, totalPaid: 0, overdueCount: 0 }

  const handleCreateInvoice = async (formData: any) => {
    const result = await createInvoice({
      subAccountId: SUB_ACCOUNT_ID,
      ...formData,
    })
    
    if (result.success) {
      setShowCreateModal(false)
      refetch()
    }
  }

  const handleSendInvoice = async (id: string) => {
    const result = await sendInvoice(id)
    if (result.success) {
      refetch()
    }
  }

  return (
    <div className="space-y-6">
      {/* Sub-tabs */}
      <div className="flex items-center gap-2 border-b border-gray-200 pb-2">
        {INVOICES_SUBTABS.map(subtab => (
          <button
            key={subtab.id}
            onClick={() => setActiveSubTab(subtab.id)}
            className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors ${
              activeSubTab === subtab.id
                ? 'bg-theme-secondary-light text-theme-secondary'
                : 'text-gray-600 hover:bg-gray-100'
            }`}
          >
            {subtab.label}
          </button>
        ))}
      </div>

      {activeSubTab === 'all' && (
        <>
          {/* Stats Cards */}
          <div className="grid grid-cols-4 gap-4">
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Total Invoices</p>
              <p className="text-2xl font-bold text-gray-900">{invoices.length}</p>
            </div>
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Outstanding</p>
              <p className="text-2xl font-bold text-orange-600">{formatCurrency(stats.totalOutstanding || 0)}</p>
            </div>
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Paid</p>
              <p className="text-2xl font-bold text-theme-success">{formatCurrency(stats.totalPaid || 0)}</p>
            </div>
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Overdue</p>
              <p className="text-2xl font-bold text-red-600">{stats.overdueCount || 0}</p>
            </div>
          </div>

          {/* Actions */}
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="relative">
                <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
                <input
                  type="text"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  placeholder="Search invoices..."
                  className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-theme-secondary focus:border-transparent"
                />
              </div>
              <select 
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
                className="px-4 py-2 border border-gray-300 rounded-lg"
              >
                <option value="">All Statuses</option>
                <option value="draft">Draft</option>
                <option value="sent">Sent</option>
                <option value="paid">Paid</option>
                <option value="overdue">Overdue</option>
              </select>
            </div>
            <button
              onClick={() => setShowCreateModal(true)}
              className="flex items-center gap-2 px-4 py-2 bg-theme-secondary text-white rounded-lg hover:opacity-90 btn-theme-primary"
            >
              <i className="fas fa-plus"></i>
              <span>Create Invoice</span>
            </button>
          </div>

          {/* Invoices Table */}
          {loading ? (
            <LoadingTable />
          ) : error ? (
            <ErrorMessage message={error} onRetry={refetch} />
          ) : invoices.length === 0 ? (
            <EmptyState
              icon="fa-file-invoice-dollar"
              title="No Invoices Yet"
              description="Create your first invoice to start getting paid"
              action="Create Invoice"
              onAction={() => setShowCreateModal(true)}
            />
          ) : (
            <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
              <table className="w-full">
                <thead className="bg-gray-50 border-b border-gray-200">
                  <tr>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Invoice</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Client</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Amount</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Status</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Due Date</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {invoices.map((invoice: any) => (
                    <tr key={invoice.id} className="hover:bg-gray-50">
                      <td className="px-4 py-3">
                        <div className="font-medium text-gray-900">{invoice.invoiceNumber}</div>
                        <div className="text-xs text-gray-500">{formatDate(invoice.issueDate || invoice.createdAt)}</div>
                      </td>
                      <td className="px-4 py-3">
                        <div className="text-sm text-gray-900">{invoice.contactName}</div>
                        <div className="text-xs text-gray-500">{invoice.contactEmail}</div>
                      </td>
                      <td className="px-4 py-3">
                        <div className="font-medium text-gray-900">{formatCurrency(invoice.amount || invoice.total, invoice.currency)}</div>
                      </td>
                      <td className="px-4 py-3">
                        <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(invoice.status)}`}>
                          {invoice.status}
                        </span>
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-500">
                        {invoice.dueDate ? formatDate(invoice.dueDate) : '-'}
                      </td>
                      <td className="px-4 py-3">
                        <div className="flex items-center gap-2">
                          <button className="p-1 text-gray-400 hover:text-blue-600" title="View">
                            <i className="fas fa-eye"></i>
                          </button>
                          <button className="p-1 text-gray-400 hover:text-theme-secondary" title="Edit">
                            <i className="fas fa-edit"></i>
                          </button>
                          {invoice.status === 'draft' && (
                            <button 
                              onClick={() => handleSendInvoice(invoice.id)}
                              className="p-1 text-gray-400 hover:text-purple-600" 
                              title="Send"
                            >
                              <i className="fas fa-paper-plane"></i>
                            </button>
                          )}
                          <button className="p-1 text-gray-400 hover:text-gray-600" title="Download">
                            <i className="fas fa-download"></i>
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </>
      )}

      {activeSubTab === 'recurring' && (
        <EmptyState
          icon="fa-redo"
          title="Recurring Invoices"
          description="Set up recurring invoices for subscription-based billing"
          action="Create Recurring Invoice"
          onAction={() => {}}
        />
      )}

      {activeSubTab === 'templates' && (
        <EmptyState
          icon="fa-file-alt"
          title="Invoice Templates"
          description="Create reusable invoice templates for faster billing"
          action="Create Template"
          onAction={() => {}}
        />
      )}

      {activeSubTab === 'estimates' && (
        <EmptyState
          icon="fa-file-invoice"
          title="Estimates"
          description="Send professional estimates to potential clients"
          action="Create Estimate"
          onAction={() => {}}
        />
      )}

      {/* Create Invoice Modal */}
      {showCreateModal && (
        <Modal
          isOpen={showCreateModal}
          onClose={() => setShowCreateModal(false)}
          title="Create Invoice"
          size="xl"
        >
          <InvoiceForm
            subAccountId={SUB_ACCOUNT_ID}
            contacts={[]}
            onSubmit={handleCreateInvoice}
            onCancel={() => setShowCreateModal(false)}
            isLoading={mutationLoading}
          />
        </Modal>
      )}
    </div>
  )
}

// ==========================================
// DOCUMENTS & CONTRACTS TAB
// ==========================================

function DocumentsTab() {
  const [activeSubTab, setActiveSubTab] = useState('all')
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [showEditorModal, setShowEditorModal] = useState(false)
  const [editingDocument, setEditingDocument] = useState<any>(null)
  const [searchQuery, setSearchQuery] = useState('')
  
  const { data, loading, error, refetch } = useDocuments(SUB_ACCOUNT_ID, { search: searchQuery })
  const { createDocument, sendDocument, loading: mutationLoading } = useDocumentMutations()

  const documents = data?.documents || []
  const stats = data?.stats || {}

  const handleCreateDocument = async (formData: any) => {
    const result = await createDocument({
      subAccountId: SUB_ACCOUNT_ID,
      ...formData,
    })
    
    if (result.success) {
      setShowCreateModal(false)
      // Open document editor
      setEditingDocument(result.data?.document)
      setShowEditorModal(true)
      refetch()
    }
  }

  const handleSendDocument = async (id: string) => {
    const result = await sendDocument(id)
    if (result.success) {
      refetch()
    }
  }

  return (
    <div className="space-y-6">
      {/* Sub-tabs */}
      <div className="flex items-center gap-2 border-b border-gray-200 pb-2">
        {DOCUMENTS_SUBTABS.map(subtab => (
          <button
            key={subtab.id}
            onClick={() => setActiveSubTab(subtab.id)}
            className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors ${
              activeSubTab === subtab.id
                ? 'bg-theme-secondary-light text-theme-secondary'
                : 'text-gray-600 hover:bg-gray-100'
            }`}
          >
            {subtab.label}
          </button>
        ))}
      </div>

      {activeSubTab === 'all' && (
        <>
          {/* Stats */}
          <div className="grid grid-cols-4 gap-4">
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Total Documents</p>
              <p className="text-2xl font-bold text-gray-900">{documents.length}</p>
            </div>
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Awaiting Signature</p>
              <p className="text-2xl font-bold text-blue-600">{stats.awaitingSignature || 0}</p>
            </div>
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Signed</p>
              <p className="text-2xl font-bold text-theme-success">{stats.signed || 0}</p>
            </div>
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <p className="text-sm text-gray-500">Expired</p>
              <p className="text-2xl font-bold text-orange-600">{stats.expired || 0}</p>
            </div>
          </div>

          {/* Actions */}
          <div className="flex items-center justify-between">
            <div className="relative">
              <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="Search documents..."
                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-theme-secondary focus:border-transparent"
              />
            </div>
            <button
              onClick={() => setShowCreateModal(true)}
              className="flex items-center gap-2 px-4 py-2 bg-theme-secondary text-white rounded-lg hover:opacity-90 btn-theme-primary"
            >
              <i className="fas fa-plus"></i>
              <span>Create Document</span>
            </button>
          </div>

          {/* Documents Table */}
          {loading ? (
            <LoadingTable />
          ) : error ? (
            <ErrorMessage message={error} onRetry={refetch} />
          ) : documents.length === 0 ? (
            <EmptyState
              icon="fa-file-signature"
              title="No Documents Yet"
              description="Create contracts, agreements, and documents with e-signature"
              action="Create Document"
              onAction={() => setShowCreateModal(true)}
            />
          ) : (
            <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
              <table className="w-full">
                <thead className="bg-gray-50 border-b border-gray-200">
                  <tr>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Document</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Recipient</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Status</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Sent</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {documents.map((doc: any) => (
                    <tr key={doc.id} className="hover:bg-gray-50">
                      <td className="px-4 py-3">
                        <div className="flex items-center gap-3">
                          <i className="fas fa-file-signature text-2xl text-gray-400"></i>
                          <div>
                            <div className="font-medium text-gray-900">{doc.title}</div>
                            <div className="text-xs text-gray-500">Created {formatDate(doc.createdAt)}</div>
                          </div>
                        </div>
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-500">{doc.contactName || 'No recipient'}</td>
                      <td className="px-4 py-3">
                        <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(doc.status)}`}>
                          {doc.status}
                        </span>
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-500">
                        {doc.sentAt ? formatDate(doc.sentAt) : '-'}
                      </td>
                      <td className="px-4 py-3">
                        <div className="flex items-center gap-2">
                          <button className="p-1 text-gray-400 hover:text-blue-600"><i className="fas fa-eye"></i></button>
                          <button 
                            onClick={() => {
                              setEditingDocument(doc)
                              setShowEditorModal(true)
                            }}
                            className="p-1 text-gray-400 hover:text-theme-secondary"
                          >
                            <i className="fas fa-edit"></i>
                          </button>
                          {doc.status === 'draft' && (
                            <button 
                              onClick={() => handleSendDocument(doc.id)}
                              className="p-1 text-gray-400 hover:text-purple-600"
                            >
                              <i className="fas fa-paper-plane"></i>
                            </button>
                          )}
                          <button className="p-1 text-gray-400 hover:text-gray-600"><i className="fas fa-download"></i></button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </>
      )}

      {activeSubTab === 'templates' && (
        <EmptyState
          icon="fa-file-contract"
          title="Document Templates"
          description="Create reusable templates for contracts, agreements, and more"
          action="Create Template"
          onAction={() => {}}
        />
      )}

      {/* Create Document Modal */}
      {showCreateModal && (
        <Modal
          isOpen={showCreateModal}
          onClose={() => setShowCreateModal(false)}
          title="Create Document"
          size="xl"
        >
          <DocumentForm
            subAccountId={SUB_ACCOUNT_ID}
            contacts={[]}
            onSubmit={handleCreateDocument}
            onCancel={() => setShowCreateModal(false)}
            isLoading={mutationLoading}
          />
        </Modal>
      )}

      {/* Document Editor Modal */}
      {showEditorModal && editingDocument && (
        <Modal
          isOpen={showEditorModal}
          onClose={() => {
            setShowEditorModal(false)
            setEditingDocument(null)
          }}
          title={`Edit: ${editingDocument.title}`}
          size="full"
        >
          <DocumentEditor
            documentContent={editingDocument.content}
            fields={editingDocument.signatureFields || []}
            signers={editingDocument.signers?.map((s: any, i: number) => ({
              id: s.id || `signer-${i}`,
              name: s.name,
              email: s.email,
              color: ['#3B82F6', '#10B981', '#F59E0B', '#EF4444'][i % 4],
            })) || []}
            onFieldsChange={(fields) => {}}
            onSignersChange={(signers) => {}}
            mode="edit"
          />
        </Modal>
      )}
    </div>
  )
}

