// ==========================================
// SUBSCRIPTIONS TAB
// ==========================================

function SubscriptionsTab() {
  const { data, loading, error, refetch } = useSubscriptions(SUB_ACCOUNT_ID)
  const { cancelSubscription, loading: mutationLoading } = useSubscriptionMutations()
  
  const subscriptions = data?.subscriptions || []
  const stats = data?.stats || {}

  const handleCancelSubscription = async (id: string) => {
    if (confirm('Are you sure you want to cancel this subscription?')) {
      const result = await cancelSubscription(id, { cancelAtPeriodEnd: true })
      if (result.success) {
        refetch()
      }
    }
  }

  return (
    <div className="space-y-6">
      {/* Stats */}
      <div className="grid grid-cols-4 gap-4">
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">Total Subscriptions</p>
          <p className="text-2xl font-bold text-gray-900">{subscriptions.length}</p>
        </div>
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">Active</p>
          <p className="text-2xl font-bold text-theme-success">{stats.active || 0}</p>
        </div>
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">MRR</p>
          <p className="text-2xl font-bold text-gray-900">{formatCurrency(stats.mrr || 0)}</p>
        </div>
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">Churn Rate</p>
          <p className="text-2xl font-bold text-gray-900">{stats.churnRate || 0}%</p>
        </div>
      </div>

      {/* Subscriptions Table */}
      {loading ? (
        <LoadingTable />
      ) : error ? (
        <ErrorMessage message={error} onRetry={refetch} />
      ) : subscriptions.length === 0 ? (
        <EmptyState
          icon="fa-sync-alt"
          title="No Subscriptions Yet"
          description="Subscriptions will appear here when customers subscribe to your plans"
        />
      ) : (
        <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
          <table className="w-full">
            <thead className="bg-gray-50 border-b border-gray-200">
              <tr>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Customer</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Plan</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Amount</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Status</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Current Period</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {subscriptions.map((sub: any) => (
                <tr key={sub.id} className="hover:bg-gray-50">
                  <td className="px-4 py-3">
                    <div className="text-sm font-medium text-gray-900">{sub.contactName}</div>
                    <div className="text-xs text-gray-500">{sub.contactEmail}</div>
                  </td>
                  <td className="px-4 py-3 text-sm text-gray-900">{sub.planName}</td>
                  <td className="px-4 py-3">
                    <div className="font-medium text-gray-900">
                      {formatCurrency(sub.amount, sub.currency)}/{sub.interval === 'monthly' ? 'mo' : 'yr'}
                    </div>
                  </td>
                  <td className="px-4 py-3">
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(sub.status)}`}>
                      {sub.status.replace('_', ' ')}
                    </span>
                  </td>
                  <td className="px-4 py-3 text-sm text-gray-500">
                    {formatDate(sub.currentPeriodStart)} - {formatDate(sub.currentPeriodEnd)}
                  </td>
                  <td className="px-4 py-3">
                    <div className="flex items-center gap-2">
                      <button className="p-1 text-gray-400 hover:text-blue-600"><i className="fas fa-eye"></i></button>
                      {sub.status === 'active' && (
                        <>
                          <button className="p-1 text-gray-400 hover:text-yellow-600"><i className="fas fa-pause"></i></button>
                          <button 
                            onClick={() => handleCancelSubscription(sub.id)}
                            className="p-1 text-gray-400 hover:text-red-600"
                          >
                            <i className="fas fa-times"></i>
                          </button>
                        </>
                      )}
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  )
}

// ==========================================
// PAYMENT LINKS TAB
// ==========================================

function PaymentLinksTab() {
  const [showCreateModal, setShowCreateModal] = useState(false)
  
  const { data, loading, error, refetch } = usePaymentLinks(SUB_ACCOUNT_ID)
  const { createPaymentLink, deletePaymentLink, loading: mutationLoading } = usePaymentLinkMutations()

  const paymentLinks = data?.paymentLinks || []

  const handleCreatePaymentLink = async (formData: any) => {
    const result = await createPaymentLink({
      subAccountId: SUB_ACCOUNT_ID,
      ...formData,
    })
    
    if (result.success) {
      setShowCreateModal(false)
      refetch()
    }
  }

  const handleDeletePaymentLink = async (id: string) => {
    if (confirm('Are you sure you want to delete this payment link?')) {
      const result = await deletePaymentLink(id)
      if (result.success) {
        refetch()
      }
    }
  }

  const copyToClipboard = (url: string) => {
    navigator.clipboard.writeText(url)
    alert('Link copied to clipboard!')
  }

  return (
    <div className="space-y-6">
      {/* Actions */}
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold text-gray-900">Payment Links</h3>
        <button
          onClick={() => setShowCreateModal(true)}
          className="flex items-center gap-2 px-4 py-2 bg-theme-secondary text-white rounded-lg hover:opacity-90 btn-theme-primary"
        >
          <i className="fas fa-plus"></i>
          <span>Create Payment Link</span>
        </button>
      </div>

      {/* Payment Links Grid */}
      {loading ? (
        <LoadingSpinner />
      ) : error ? (
        <ErrorMessage message={error} onRetry={refetch} />
      ) : paymentLinks.length === 0 ? (
        <EmptyState
          icon="fa-link"
          title="No Payment Links Yet"
          description="Create shareable payment links for quick payments"
          action="Create Payment Link"
          onAction={() => setShowCreateModal(true)}
        />
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {paymentLinks.map((link: any) => (
            <div key={link.id} className="bg-white rounded-lg border border-gray-200 p-4">
              <div className="flex items-start justify-between mb-3">
                <div>
                  <h4 className="font-medium text-gray-900">{link.name}</h4>
                  <p className="text-2xl font-bold text-theme-secondary mt-1">{formatCurrency(link.amount, link.currency)}</p>
                </div>
                <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(link.status)}`}>
                  {link.status}
                </span>
              </div>
              <div className="flex items-center gap-2 mb-3 p-2 bg-gray-50 rounded-lg">
                <input
                  type="text"
                  value={link.url}
                  readOnly
                  className="flex-1 text-sm text-gray-600 bg-transparent border-none focus:ring-0 truncate"
                />
                <button 
                  onClick={() => copyToClipboard(link.url)}
                  className="p-1 text-gray-400 hover:text-gray-600"
                >
                  <i className="fas fa-copy"></i>
                </button>
              </div>
              <div className="flex items-center justify-between text-sm text-gray-500">
                <span>Used {link.usageCount}{link.usageLimit ? `/${link.usageLimit}` : ''} times</span>
                {link.expiresAt && (
                  <span>Expires {formatDate(link.expiresAt)}</span>
                )}
              </div>
              <div className="flex items-center gap-2 mt-3 pt-3 border-t border-gray-200">
                <button className="flex-1 px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-lg">
                  <i className="fas fa-edit mr-1"></i> Edit
                </button>
                <button className="flex-1 px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-lg">
                  <i className="fas fa-chart-bar mr-1"></i> Stats
                </button>
                <button 
                  onClick={() => handleDeletePaymentLink(link.id)}
                  className="px-3 py-2 text-sm text-red-600 hover:bg-red-50 rounded-lg"
                >
                  <i className="fas fa-trash"></i>
                </button>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Create Payment Link Modal */}
      {showCreateModal && (
        <Modal
          isOpen={showCreateModal}
          onClose={() => setShowCreateModal(false)}
          title="Create Payment Link"
          size="lg"
        >
          <PaymentLinkForm
            subAccountId={SUB_ACCOUNT_ID}
            onSubmit={handleCreatePaymentLink}
            onCancel={() => setShowCreateModal(false)}
            isLoading={mutationLoading}
          />
        </Modal>
      )}
    </div>
  )
}

// ==========================================
// TRANSACTIONS TAB
// ==========================================

function TransactionsTab() {
  const [statusFilter, setStatusFilter] = useState('')
  const [typeFilter, setTypeFilter] = useState('')
  
  const { data, loading, error, refetch } = usePayments(SUB_ACCOUNT_ID, {
    status: statusFilter || undefined,
  })
  const { refundPayment, loading: mutationLoading } = usePaymentMutations()

  const transactions = data?.payments || []
  const summary = data?.summary || {}

  const getTypeIcon = (type: string) => {
    const icons: Record<string, string> = {
      payment: 'fa-arrow-down text-green-500',
      refund: 'fa-arrow-up text-red-500',
      payout: 'fa-arrow-up text-blue-500',
      fee: 'fa-minus text-gray-500',
    }
    return icons[type] || 'fa-circle text-gray-500'
  }

  const handleRefund = async (id: string) => {
    if (confirm('Are you sure you want to refund this payment?')) {
      const result = await refundPayment(id, {})
      if (result.success) {
        refetch()
      }
    }
  }

  return (
    <div className="space-y-6">
      {/* Stats */}
      <div className="grid grid-cols-4 gap-4">
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">Total Transactions</p>
          <p className="text-2xl font-bold text-gray-900">{transactions.length}</p>
        </div>
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">Payments</p>
          <p className="text-2xl font-bold text-theme-success">{formatCurrency(summary.totalPayments || 0)}</p>
        </div>
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">Refunds</p>
          <p className="text-2xl font-bold text-red-600">{formatCurrency(summary.totalRefunds || 0)}</p>
        </div>
        <div className="bg-white rounded-lg border border-gray-200 p-4">
          <p className="text-sm text-gray-500">Net Revenue</p>
          <p className="text-2xl font-bold text-gray-900">{formatCurrency(summary.netRevenue || 0)}</p>
        </div>
      </div>

      {/* Filters */}
      <div className="flex items-center gap-3">
        <select 
          value={typeFilter}
          onChange={(e) => setTypeFilter(e.target.value)}
          className="px-4 py-2 border border-gray-300 rounded-lg"
        >
          <option value="">All Types</option>
          <option value="payment">Payments</option>
          <option value="refund">Refunds</option>
          <option value="payout">Payouts</option>
        </select>
        <select 
          value={statusFilter}
          onChange={(e) => setStatusFilter(e.target.value)}
          className="px-4 py-2 border border-gray-300 rounded-lg"
        >
          <option value="">All Statuses</option>
          <option value="succeeded">Succeeded</option>
          <option value="pending">Pending</option>
          <option value="failed">Failed</option>
        </select>
      </div>

      {/* Transactions Table */}
      {loading ? (
        <LoadingTable />
      ) : error ? (
        <ErrorMessage message={error} onRetry={refetch} />
      ) : transactions.length === 0 ? (
        <EmptyState
          icon="fa-exchange-alt"
          title="No Transactions Yet"
          description="Transactions will appear here when payments are processed"
        />
      ) : (
        <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
          <table className="w-full">
            <thead className="bg-gray-50 border-b border-gray-200">
              <tr>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Type</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Description</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Customer</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Amount</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Status</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Date</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {transactions.map((txn: any) => (
                <tr key={txn.id} className="hover:bg-gray-50">
                  <td className="px-4 py-3">
                    <div className="flex items-center gap-2">
                      <i className={`fas ${getTypeIcon(txn.type)}`}></i>
                      <span className="text-sm capitalize">{txn.type}</span>
                    </div>
                  </td>
                  <td className="px-4 py-3">
                    <div className="text-sm text-gray-900">{txn.description}</div>
                    {txn.paymentMethod && (
                      <div className="text-xs text-gray-500">{txn.paymentMethod}</div>
                    )}
                  </td>
                  <td className="px-4 py-3 text-sm text-gray-500">{txn.contactName || '-'}</td>
                  <td className="px-4 py-3">
                    <span className={`font-medium ${txn.amount >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                      {txn.amount >= 0 ? '+' : ''}{formatCurrency(Math.abs(txn.amount), txn.currency)}
                    </span>
                  </td>
                  <td className="px-4 py-3">
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(txn.status)}`}>
                      {txn.status}
                    </span>
                  </td>
                  <td className="px-4 py-3 text-sm text-gray-500">
                    {formatDate(txn.createdAt)}
                  </td>
                  <td className="px-4 py-3">
                    <div className="flex items-center gap-2">
                      <button className="p-1 text-gray-400 hover:text-blue-600"><i className="fas fa-eye"></i></button>
                      {txn.type === 'payment' && txn.status === 'succeeded' && (
                        <button 
                          onClick={() => handleRefund(txn.id)}
                          className="p-1 text-gray-400 hover:text-red-600"
                          title="Refund"
                        >
                          <i className="fas fa-undo"></i>
                        </button>
                      )}
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  )
}

// ==========================================
// PRODUCTS TAB
// ==========================================

function ProductsTab() {
  const [activeSubTab, setActiveSubTab] = useState('products')
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  
  const { data, loading, error, refetch } = useProducts(SUB_ACCOUNT_ID, { search: searchQuery })
  const { createProduct, deleteProduct, loading: mutationLoading } = useProductMutations()

  const products = data?.products || []

  const handleCreateProduct = async (formData: any) => {
    const result = await createProduct({
      subAccountId: SUB_ACCOUNT_ID,
      ...formData,
    })
    
    if (result.success) {
      setShowCreateModal(false)
      refetch()
    }
  }

  const handleDeleteProduct = async (id: string) => {
    if (confirm('Are you sure you want to delete this product?')) {
      const result = await deleteProduct(id)
      if (result.success) {
        refetch()
      }
    }
  }

  return (
    <div className="space-y-6">
      {/* Sub-tabs */}
      <div className="flex items-center gap-2 border-b border-gray-200 pb-2">
        {PRODUCTS_SUBTABS.map(subtab => (
          <button
            key={subtab.id}
            onClick={() => setActiveSubTab(subtab.id)}
            className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors ${
              activeSubTab === subtab.id
                ? 'bg-theme-secondary-light text-theme-secondary'
                : 'text-gray-600 hover:bg-gray-100'
            }`}
          >
            {subtab.label}
          </button>
        ))}
      </div>

      {activeSubTab === 'products' && (
        <>
          <div className="flex items-center justify-between">
            <div className="relative">
              <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="Search products..."
                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg"
              />
            </div>
            <button
              onClick={() => setShowCreateModal(true)}
              className="flex items-center gap-2 px-4 py-2 bg-theme-secondary text-white rounded-lg hover:opacity-90 btn-theme-primary"
            >
              <i className="fas fa-plus"></i>
              <span>Add Product</span>
            </button>
          </div>

          {loading ? (
            <LoadingSpinner />
          ) : error ? (
            <ErrorMessage message={error} onRetry={refetch} />
          ) : products.length === 0 ? (
            <EmptyState
              icon="fa-box"
              title="No Products Yet"
              description="Add products to start selling"
              action="Add Product"
              onAction={() => setShowCreateModal(true)}
            />
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {products.map((product: any) => (
                <div key={product.id} className="bg-white rounded-lg border border-gray-200 overflow-hidden hover:shadow-lg transition-shadow">
                  <div className="h-32 bg-gray-100 flex items-center justify-center">
                    {product.images?.[0] ? (
                      <img src={product.images[0]} alt={product.name} className="w-full h-full object-cover" />
                    ) : (
                      <i className={`fas ${product.type === 'digital' ? 'fa-download' : product.type === 'service' ? 'fa-concierge-bell' : 'fa-box'} text-4xl text-gray-400`}></i>
                    )}
                  </div>
                  <div className="p-4">
                    <div className="flex items-start justify-between mb-2">
                      <h4 className="font-medium text-gray-900">{product.name}</h4>
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(product.status)}`}>
                        {product.status}
                      </span>
                    </div>
                    {product.description && (
                      <p className="text-sm text-gray-500 mb-2 line-clamp-2">{product.description}</p>
                    )}
                    <div className="flex items-center justify-between">
                      <span className="text-lg font-bold text-theme-secondary">{formatCurrency(product.price, product.currency)}</span>
                      <span className="text-xs text-gray-500 capitalize">{product.type}</span>
                    </div>
                    {product.sku && (
                      <div className="text-xs text-gray-400 mt-2">SKU: {product.sku}</div>
                    )}
                    <div className="flex items-center gap-2 mt-3 pt-3 border-t border-gray-200">
                      <button className="flex-1 px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-lg">
                        <i className="fas fa-edit mr-1"></i> Edit
                      </button>
                      <button 
                        onClick={() => handleDeleteProduct(product.id)}
                        className="px-3 py-2 text-sm text-red-600 hover:bg-red-50 rounded-lg"
                      >
                        <i className="fas fa-trash"></i>
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </>
      )}

      {activeSubTab === 'collections' && (
        <EmptyState
          icon="fa-folder"
          title="Collections"
          description="Organize your products into collections"
          action="Create Collection"
          onAction={() => {}}
        />
      )}

      {activeSubTab === 'inventory' && (
        <EmptyState
          icon="fa-warehouse"
          title="Inventory Management"
          description="Track stock levels for physical products"
          action="Configure Inventory"
          onAction={() => {}}
        />
      )}

      {/* Create Product Modal */}
      {showCreateModal && (
        <Modal
          isOpen={showCreateModal}
          onClose={() => setShowCreateModal(false)}
          title="Add Product"
          size="xl"
        >
          <ProductForm
            subAccountId={SUB_ACCOUNT_ID}
            onSubmit={handleCreateProduct}
            onCancel={() => setShowCreateModal(false)}
            isLoading={mutationLoading}
          />
        </Modal>
      )}
    </div>
  )
}

// ==========================================
// COUPONS TAB
// ==========================================

function CouponsTab() {
  const [showCreateModal, setShowCreateModal] = useState(false)
  
  const { data, loading, error, refetch } = useCoupons(SUB_ACCOUNT_ID)
  const { createCoupon, deleteCoupon, loading: mutationLoading } = useCouponMutations()

  const coupons = data?.coupons || []

  const handleCreateCoupon = async (formData: any) => {
    const result = await createCoupon({
      subAccountId: SUB_ACCOUNT_ID,
      ...formData,
    })
    
    if (result.success) {
      setShowCreateModal(false)
      refetch()
    }
  }

  const handleDeleteCoupon = async (id: string) => {
    if (confirm('Are you sure you want to delete this coupon?')) {
      const result = await deleteCoupon(id)
      if (result.success) {
        refetch()
      }
    }
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold text-gray-900">Discount Coupons</h3>
        <button
          onClick={() => setShowCreateModal(true)}
          className="flex items-center gap-2 px-4 py-2 bg-theme-secondary text-white rounded-lg hover:opacity-90 btn-theme-primary"
        >
          <i className="fas fa-plus"></i>
          <span>Create Coupon</span>
        </button>
      </div>

      {loading ? (
        <LoadingTable />
      ) : error ? (
        <ErrorMessage message={error} onRetry={refetch} />
      ) : coupons.length === 0 ? (
        <EmptyState
          icon="fa-tags"
          title="No Coupons Yet"
          description="Create discount coupons to boost sales"
          action="Create Coupon"
          onAction={() => setShowCreateModal(true)}
        />
      ) : (
        <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
          <table className="w-full">
            <thead className="bg-gray-50 border-b border-gray-200">
              <tr>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Code</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Discount</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Usage</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Valid Until</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Status</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {coupons.map((coupon: any) => (
                <tr key={coupon.id} className="hover:bg-gray-50">
                  <td className="px-4 py-3">
                    <code className="px-2 py-1 bg-gray-100 rounded text-sm font-mono">{coupon.code}</code>
                  </td>
                  <td className="px-4 py-3 font-medium text-gray-900">
                    {coupon.discountType === 'percentage' ? `${coupon.discountValue}% off` : formatCurrency(coupon.discountValue, coupon.currency) + ' off'}
                  </td>
                  <td className="px-4 py-3 text-sm text-gray-500">
                    {coupon.usageCount}{coupon.usageLimit ? `/${coupon.usageLimit}` : ''} used
                  </td>
                  <td className="px-4 py-3 text-sm text-gray-500">
                    {coupon.validUntil ? formatDate(coupon.validUntil) : 'No expiry'}
                  </td>
                  <td className="px-4 py-3">
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(coupon.status)}`}>
                      {coupon.status}
                    </span>
                  </td>
                  <td className="px-4 py-3">
                    <div className="flex items-center gap-2">
                      <button className="p-1 text-gray-400 hover:text-blue-600"><i className="fas fa-eye"></i></button>
                      <button className="p-1 text-gray-400 hover:text-theme-secondary"><i className="fas fa-edit"></i></button>
                      <button 
                        onClick={() => handleDeleteCoupon(coupon.id)}
                        className="p-1 text-gray-400 hover:text-red-600"
                      >
                        <i className="fas fa-trash"></i>
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* Create Coupon Modal */}
      {showCreateModal && (
        <Modal
          isOpen={showCreateModal}
          onClose={() => setShowCreateModal(false)}
          title="Create Coupon"
          size="lg"
        >
          <CouponForm
            subAccountId={SUB_ACCOUNT_ID}
            onSubmit={handleCreateCoupon}
            onCancel={() => setShowCreateModal(false)}
            isLoading={mutationLoading}
          />
        </Modal>
      )}
    </div>
  )
}

// ==========================================
// SETTINGS TAB
// ==========================================

function SettingsTab() {
  const { data, loading, error, refetch } = useSettings(SUB_ACCOUNT_ID)
  const { updateSettings, addTaxRate, loading: mutationLoading } = useSettingsMutations()

  const [settings, setSettings] = useState({
    currency: 'USD',
    taxEnabled: true,
    taxRate: 10,
    invoicePrefix: 'INV',
    estimatePrefix: 'EST',
    orderPrefix: 'ORD',
    paymentTerms: 30,
    autoReminders: true,
  })

  useEffect(() => {
    if (data?.settings) {
      setSettings(prev => ({ ...prev, ...data.settings }))
    }
  }, [data])

  const handleSave = async () => {
    const result = await updateSettings({
      subAccountId: SUB_ACCOUNT_ID,
      ...settings,
    })
    
    if (result.success) {
      alert('Settings saved successfully!')
      refetch()
    }
  }

  if (loading) return <LoadingSpinner />
  if (error) return <ErrorMessage message={error} onRetry={refetch} />

  return (
    <div className="space-y-6 max-w-3xl">
      {/* General Settings */}
      <div className="bg-white rounded-lg border border-gray-200 p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">General Settings</h3>
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Default Currency</label>
            <select
              value={settings.currency}
              onChange={(e) => setSettings({ ...settings, currency: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg"
            >
              <option value="USD">USD - US Dollar</option>
              <option value="EUR">EUR - Euro</option>
              <option value="GBP">GBP - British Pound</option>
              <option value="CAD">CAD - Canadian Dollar</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Default Payment Terms (days)</label>
            <input
              type="number"
              value={settings.paymentTerms}
              onChange={(e) => setSettings({ ...settings, paymentTerms: parseInt(e.target.value) })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg"
            />
          </div>
        </div>
      </div>

      {/* Tax Settings */}
      <div className="bg-white rounded-lg border border-gray-200 p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Tax Settings</h3>
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <div>
              <h4 className="text-sm font-medium text-gray-900">Enable Tax</h4>
              <p className="text-sm text-gray-500">Add tax to invoices and orders</p>
            </div>
            <button
              type="button"
              onClick={() => setSettings({ ...settings, taxEnabled: !settings.taxEnabled })}
              className={`relative inline-flex h-6 w-11 flex-shrink-0 cursor-pointer rounded-full border-2 border-transparent transition-colors ${
                settings.taxEnabled ? 'bg-theme-secondary' : 'bg-gray-200'
              }`}
            >
              <span className={`pointer-events-none inline-block h-5 w-5 transform rounded-full bg-white shadow transition ${
                settings.taxEnabled ? 'translate-x-5' : 'translate-x-0'
              }`} />
            </button>
          </div>
          {settings.taxEnabled && (
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Default Tax Rate (%)</label>
              <input
                type="number"
                value={settings.taxRate}
                onChange={(e) => setSettings({ ...settings, taxRate: parseFloat(e.target.value) })}
                className="w-full px-4 py-2 border border-gray-300 rounded-lg"
              />
            </div>
          )}
        </div>
      </div>

      {/* Number Prefixes */}
      <div className="bg-white rounded-lg border border-gray-200 p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Number Prefixes</h3>
        <div className="grid grid-cols-3 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Invoice Prefix</label>
            <input
              type="text"
              value={settings.invoicePrefix}
              onChange={(e) => setSettings({ ...settings, invoicePrefix: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Estimate Prefix</label>
            <input
              type="text"
              value={settings.estimatePrefix}
              onChange={(e) => setSettings({ ...settings, estimatePrefix: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Order Prefix</label>
            <input
              type="text"
              value={settings.orderPrefix}
              onChange={(e) => setSettings({ ...settings, orderPrefix: e.target.value })}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg"
            />
          </div>
        </div>
      </div>

      {/* Reminder Settings */}
      <div className="bg-white rounded-lg border border-gray-200 p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Payment Reminders</h3>
        <div className="flex items-center justify-between">
          <div>
            <h4 className="text-sm font-medium text-gray-900">Auto Reminders</h4>
            <p className="text-sm text-gray-500">Automatically send payment reminders</p>
          </div>
          <button
            type="button"
            onClick={() => setSettings({ ...settings, autoReminders: !settings.autoReminders })}
            className={`relative inline-flex h-6 w-11 flex-shrink-0 cursor-pointer rounded-full border-2 border-transparent transition-colors ${
              settings.autoReminders ? 'bg-theme-secondary' : 'bg-gray-200'
            }`}
          >
            <span className={`pointer-events-none inline-block h-5 w-5 transform rounded-full bg-white shadow transition ${
              settings.autoReminders ? 'translate-x-5' : 'translate-x-0'
            }`} />
          </button>
        </div>
      </div>

      {/* Save Button */}
      <div className="flex justify-end">
        <button 
          onClick={handleSave}
          disabled={mutationLoading}
          className="px-6 py-2 bg-theme-secondary text-white rounded-lg hover:opacity-90 disabled:opacity-50 btn-theme-primary"
        >
          {mutationLoading ? (
            <span className="flex items-center gap-2">
              <i className="fas fa-spinner fa-spin"></i>
              Saving...
            </span>
          ) : (
            'Save Settings'
          )}
        </button>
      </div>
    </div>
  )
}

// ==========================================
// INTEGRATIONS TAB
// ==========================================

function IntegrationsTab() {
  const [integrations, setIntegrations] = useState([
    { id: 'stripe', name: 'Stripe', provider: 'stripe', status: 'disconnected', isDefault: false },
    { id: 'paypal', name: 'PayPal', provider: 'paypal', status: 'disconnected', isDefault: false },
  ])

  const availableIntegrations = [
    { provider: 'stripe', name: 'Stripe', icon: 'fab fa-stripe', color: '#6772E5', description: 'Accept credit cards, Apple Pay, Google Pay, and more' },
    { provider: 'paypal', name: 'PayPal', icon: 'fab fa-paypal', color: '#003087', description: 'Accept PayPal payments worldwide' },
    { provider: 'authorize_net', name: 'Authorize.net', icon: 'fas fa-credit-card', color: '#1A4596', description: 'Accept payments via Authorize.net gateway' },
    { provider: 'nmi', name: 'NMI', icon: 'fas fa-credit-card', color: '#00A1E0', description: 'Network Merchants payment gateway' },
    { provider: 'square', name: 'Square', icon: 'fas fa-square', color: '#000000', description: 'Accept payments with Square' },
    { provider: 'paytabs', name: 'PayTabs', icon: 'fas fa-credit-card', color: '#1E88E5', description: 'Middle East & Africa payment gateway' },
    { provider: 'paymob', name: 'PayMob', icon: 'fas fa-credit-card', color: '#FF5722', description: 'Egypt & MENA payment solutions' },
    { provider: 'payplus', name: 'PayPlus', icon: 'fas fa-credit-card', color: '#4CAF50', description: 'Israeli payment gateway' },
  ]

  const getIntegrationStatus = (provider: string) => {
    const integration = integrations.find(i => i.provider === provider)
    return integration?.status || 'disconnected'
  }

  const handleConnect = async (provider: string) => {
    // In a real app, this would open OAuth flow or API key input
    alert(`Connect to ${provider} - This would open the authentication flow`)
  }

  const handleDisconnect = async (provider: string) => {
    if (confirm(`Are you sure you want to disconnect ${provider}?`)) {
      setIntegrations(prev => 
        prev.map(i => i.provider === provider ? { ...i, status: 'disconnected', isDefault: false } : i)
      )
    }
  }

  const handleSetDefault = (provider: string) => {
    setIntegrations(prev => 
      prev.map(i => ({ ...i, isDefault: i.provider === provider }))
    )
  }

  return (
    <div className="space-y-6">
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-2">Payment Integrations</h3>
        <p className="text-gray-500">Connect your payment gateways to accept payments</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {availableIntegrations.map(integration => {
          const status = getIntegrationStatus(integration.provider)
          const isConnected = status === 'connected'
          const existingIntegration = integrations.find(i => i.provider === integration.provider)

          return (
            <div
              key={integration.provider}
              className={`bg-white rounded-lg border p-4 ${
                isConnected ? 'border-green-200 bg-green-50/50' : 'border-gray-200'
              }`}
            >
              <div className="flex items-start justify-between">
                <div className="flex items-center gap-3">
                  <div
                    className="w-12 h-12 rounded-lg flex items-center justify-center"
                    style={{ backgroundColor: `${integration.color}15` }}
                  >
                    <i className={`${integration.icon} text-xl`} style={{ color: integration.color }}></i>
                  </div>
                  <div>
                    <h4 className="font-medium text-gray-900">{integration.name}</h4>
                    <p className="text-sm text-gray-500">{integration.description}</p>
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  {existingIntegration?.isDefault && (
                    <span className="px-2 py-1 text-xs font-medium bg-yellow-100 text-yellow-700 rounded-full">
                      Default
                    </span>
                  )}
                  <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(status)}`}>
                    {status}
                  </span>
                </div>
              </div>
              <div className="flex items-center gap-2 mt-4">
                {isConnected ? (
                  <>
                    <button className="flex-1 px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-lg border border-gray-200">
                      <i className="fas fa-cog mr-1"></i> Settings
                    </button>
                    {!existingIntegration?.isDefault && (
                      <button 
                        onClick={() => handleSetDefault(integration.provider)}
                        className="px-3 py-2 text-sm text-green-600 hover:bg-green-50 rounded-lg border border-green-200"
                      >
                        Set Default
                      </button>
                    )}
                    <button 
                      onClick={() => handleDisconnect(integration.provider)}
                      className="px-3 py-2 text-sm text-red-600 hover:bg-red-50 rounded-lg border border-red-200"
                    >
                      Disconnect
                    </button>
                  </>
                ) : (
                  <button 
                    onClick={() => handleConnect(integration.provider)}
                    className="flex-1 px-3 py-2 text-sm text-white bg-theme-secondary hover:opacity-90 rounded-lg btn-theme-primary"
                  >
                    <i className="fas fa-plug mr-1"></i> Connect
                  </button>
                )}
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

